<template>
  <div class="container">
    <div class="row" v-if="isLoading">
      <b-spinner variant="primary" label="Spinning"></b-spinner>
    </div>

    <div class="row my-3" v-if="!isLoading">
      <div class="col-lg-10">
        <h1>{{ step.name }}</h1>
      </div>
      <div class="col-lg-2 text-right">
        <b-button :to="{ name: 'edit_step', params: { id: step.id } }" variant="light">Edit</b-button>
        <b-button @click="deleteStep" variant="danger">Delete</b-button>
      </div>
    </div>

    <div class="row" v-if="!isLoading">
      <div class="col-lg">
        <dl>
          <dt>Workflow</dt>
          <dd>{{ step.workflow_name }}</dd>
          <dt>Order in workflow</dt>
          <dd>{{ step.order }}</dd>
          <dt>Description</dt>
          <dd>{{ step.description }}</dd>
          <dt>Created</dt>
          <dd>{{ toLocalTime(step.created) }}</dd>
          <dt>Updated</dt>
          <dd>{{ toLocalTime(step.updated) }}</dd>
        </dl>
      </div>
    </div>

    <div class="row my-4" v-if="!isLoading">
      <div class="col-lg-11">
        <h3>
          Task Templates
        </h3>
      </div>
      <div class="col-lg-1">
        <b-button variant="primary" :to="{ 'name': 'new_task_template', params: { 'stepId': this.id } }">New</b-button>
      </div>
    </div>
    <div class="row" v-if="!isLoading">
      <div class="col-lg">
        <task-template-table :step-id="step.id" :task-templates="step.task_templates"/>
      </div>
    </div>
  </div>
</template>

<script>
import {toLocalTime} from "@/convert_date";
import Backend from "@/backend";
import TaskTemplateTable from "@/components/TaskTemplateTable.vue";
import {formatErrors} from "@/utils";

export default {
  name: "StepView",
  components: {
    'task-template-table': TaskTemplateTable
  },
  props: {
    id: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      step: null,
      isLoading: true,
    };
  },
  async created() {
    this.getStep();
  },
  methods: {
    toLocalTime,
    getStep() {
      Backend.getStep(this.id).then(step => {
        this.step = step;
        this.isLoading = false;
      }).catch(statusCode => {
        if (statusCode === 404) {
          this.$router.push({name: 'workflows'});
          this.$store.commit("setAlertError", "Step not found.");
        } else {
          console.log(statusCode);
        }
      });
    },
    deleteStep() {
      if (confirm("Are you sure? This action cannot be undone.")) {
        Backend.deleteStep(this.id).then(() => {
          this.$router.push({ name: 'workflow', params: { id: String(this.step.workflow_id) } });
          this.$store.commit("setAlertSuccess", "Step deleted successfully.");
        }).catch(errors => {
          this.$store.commit("setAlertError", formatErrors("Step could not be deleted", errors));
        })
      }
    },
  }
}
</script>

