<template>
  <div class="container">
    <div class="row my-3">
      <div class="col-lg">
        <h1>{{ title() }}</h1>
      </div>
    </div>

    <div class="row">
      <div class="col-lg">
        <b-form @submit="save">
          <b-form-group
            id="step_group"
            label="Step"
            label-for="step"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-select
              id="step"
              v-model="taskTemplate.step_id"
              :options="stepOptions"
              disabled
              required
            ></b-form-select>
          </b-form-group>

          <b-form-group
            id="name_group"
            label="Name"
            label-for="name"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="name"
              v-model="taskTemplate.name"
              type="text"
              required
            >
            </b-form-input>
          </b-form-group>

          <b-form-group
            id="description_group"
            label="Description"
            label-for="description"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-textarea
              id="textarea"
              v-model="taskTemplate.description"
              rows="5"
              max-rows="5"
            />
          </b-form-group>

          <b-form-group
            id="user_group"
            label="User"
            label-for="user"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-select
              id="user"
              v-model="taskTemplate.user_id"
              :options="userOptions"
              required
            ></b-form-select>
          </b-form-group>

          <div class="text-right">
            <b-button @click="cancel" variant="link">Cancel</b-button>
            <b-button type="submit" variant="primary">Save</b-button>
          </div>
        </b-form>
      </div>
    </div>
  </div>
</template>

<script>
import Backend from "@/backend";
import {formatErrors} from "@/utils";

export default {
  name: "TaskTemplate",
  props: {
    id: Number,
    stepId: Number,
  },
  data() {
    return {
      taskTemplate: {},
      stepOptions: [],
      userOptions: [],
      realStepId: null,
    };
  },
  async created() {
    if (this.stepId != null) {
      this.realStepId = this.stepId;
    }
    if (this.id) {
      this.getTaskTemplate();
    } else {
      this.getStepOptions();
    }
    this.getUserOptions();
  },
  methods: {
    title() {
      return this.persisted() ? "Edit Task Template" : "Add Task Template";
    },
    persisted() {
      return this.id != null;
    },
    getTaskTemplate() {
      Backend.getTaskTemplate(this.id).then(taskTemplate => {
        this.taskTemplate = taskTemplate;
        this.realStepId = this.taskTemplate.step_id;
        this.getStepOptions();
      }).catch(statusCode => {
        if (statusCode === 404) {
          this.$router.push({ name: 'step', params: { id: String(this.stepId) }});
          this.$store.commit("setAlertError", "Task template not found.");
        } else {
          console.log(statusCode);
        }
      });
    },
    getStepOptions() {
      Backend.getStep(this.realStepId).then(step => {
        const display = step.workflow_name + " - " + step.name
        this.stepOptions.push({ value: step.id, text: display });
        if (this.stepId != null) {
          this.taskTemplate.step_id = this.stepId;
        }
      }).catch(statusCode => {
        if (statusCode === 404) {
          this.$router.push({ name: 'step', params: { id: String(this.stepId) } });
          this.$store.commit("setAlertError", "Workflow not found.");
        } else {
          console.log(statusCode);
        }
      });
    },
    getUserOptions() {
      Backend.getUsers().then(users => {
        users.forEach(user => {
          this.userOptions.push({ value: user.id, text: user.name });
        });
      }).catch(statusCode => {
        if (statusCode === 404) {
          this.$store.commit("setAlertError", "Error retrieving user options.");
        } else {
          console.log(statusCode);
        }
      });
    },
    cancel() {
      this.$router.push({ name: 'step', params: { id: String(this.taskTemplate.stepId) } });
    },
    save() {
      if (this.persisted()) {
        Backend.updateTaskTemplate(this.id, this.taskTemplate).then(() => {
          this.$router.push({ name: 'step', params: { id: String(this.realStepId) } });
          this.$store.commit("setAlertSuccess", "Task template updated successfully.");
        }).catch((errors) => {
          this.$store.commit("setAlertError", formatErrors("Task template could not be saved", errors));
        });
      } else {
        Backend.createTaskTemplate(this.taskTemplate).then(() => {
          this.$router.push({ name: 'step', params: { id: String(this.realStepId) } });
          this.$store.commit("setAlertSuccess", "Task template created successfully.");
        }).catch((errors) => {
          this.$store.commit("setAlertError", formatErrors("Task template could not be saved", errors));
        });
      }
    },
  }
}
</script>

