<template>
  <div>
    <b-input v-model="filter" type="text" placeholder="Type to filter"/>
    <b-table
        id="step-table"
        bordered
        striped
        show-empty
        responsive
        :items="steps"
        :fields="fields"
        sort-by="order"
        :filter="filter"
        :per-page="pageSize"
        :current-page="currentPage"
        @filtered="filtered"
    >
      <template #cell(name)="data">
        <router-link :to="{ name: 'step', params: { id: data.item.id } }">{{ data.item.name }}</router-link>
      </template>
    </b-table>
    <b-pagination
      v-model="currentPage"
      :total-rows="rowCount"
      :per-page="pageSize"
      aria-controls="step-table"
    />
  </div>
</template>

<script>
export default {
  name: "StepTable",
  props: {
    steps: {
      type: Array,
      required: true,
    },
    pageSize: {
      type: Number,
      default: 10,
    },
  },
  data() {
    return {
      filter: null,
      fields: ['order', 'name', 'description'],
      currentPage: 1,
      rowCount: null,
    };
  },
  created() {
    this.rowCount = this.steps.length;
  },
  methods: {
    filtered(items) {
      this.rowCount = items.length;
      this.currentPage = 1;
    },
  }
}
</script>

