<template>
  <div class="container">
    <div class="row my-3">
      <div class="col-lg-11">
        <h1>Workflows</h1>
      </div>
      <div class="col-lg">
        <b-button variant="primary" :to="{ 'name': 'new_workflow' }">New</b-button>
      </div>
    </div>
    <div class="row">
      <div class="col-lg">
        <workflow-table :workflows="workflows"></workflow-table>
      </div>
    </div>
  </div>
</template>

<script>
import WorkflowTable from "@/components/WorkflowTable.vue";
import Backend from "@/backend";

export default {
  name: "WorkflowList",
  components: {
    "workflow-table": WorkflowTable,
  },
  data() {
    return {
      workflows: [],
    };
  },
  async created() {
    this.getWorkflows();
  },
  methods: {
    getWorkflows() {
      Backend.getWorkflows().then(workflows => {
        this.workflows = workflows;
      }).catch(() => {
        this.$store.commit("setAlertError", "Error retrieving list of workflows");
      });
    }
  },
}
</script>

