<template>
  <div class="container">
    <div class="row my-3">
      <div class="col-lg">
        <h1>Outstanding Balances Report</h1>
      </div>
    </div>

    <div class="row">
      <div class="col-lg">
        <b-table bordered striped show-empty responsive :items="reportRecords" :fields="fields" sort-by="amount" sort-desc>
          <template #cell(title)="data">
            <router-link :to="{ name: 'matter', params: { id: data.item.id } }">{{ data.item.title }}</router-link>
          </template>
        </b-table>
      </div>
    </div>
  </div>
</template>

<script>
import Backend from "@/backend";
import {Money} from "@/money";

export default {
  name: "OutstandingBalancesReport",
  data() {
    return {
      reportRecords: [],
      fields: [
        { key: 'title', label: "Matter" },
        { key: 'amount', formatter: Money.format },
      ]
    };
  },
  async created() {
    this.getReportRecords();
  },
  methods: {
    getReportRecords() {
      Backend.getReport("outstanding-balances").then(reportRecords => {
        this.reportRecords = reportRecords;
      }).catch(statusCode => {
        console.log(statusCode);
      });
    },
  },
}
</script>

