<template>
  <div class="container">
    <div class="row my-3">
      <div class="col-lg">
        <h1>{{ title() }}</h1>
      </div>
    </div>

    <div class="row">
      <div class="col-lg">
        <b-form @submit="save">
          <b-form-group
            id="first_name_group"
            label="First name"
            label-for="first_name"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="first_name"
              v-model="client.first_name"
              type="text"
              required
            ></b-form-input>
          </b-form-group>

          <b-form-group
            id="middle_name_group"
            label="Middle name"
            label-for="middle_name"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="middle_name"
              v-model="client.middle_name"
              type="text"
            ></b-form-input>
          </b-form-group>

          <b-form-group
            id="last_name_group"
            label="Last name"
            label-for="last_name"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="last_name"
              v-model="client.last_name"
              type="text"
              required
            ></b-form-input>
          </b-form-group>

          <b-form-group
            id="suffix_group"
            label="Suffix"
            label-for="suffix"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-select
              id="suffix"
              v-model="client.suffix"
              :options="suffixOptions"
            ></b-form-select>
          </b-form-group>

          <b-form-group
            id="address_1_group"
            label="Address"
            label-for="address_1"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="address_1"
              v-model="client.address_1"
              type="text"
              required
            ></b-form-input>
          </b-form-group>

          <b-form-group
            id="address_2_group"
            label="Address 2"
            label-for="address_2"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="address_2"
              v-model="client.address_2"
              type="text"
            ></b-form-input>
          </b-form-group>

          <b-form-group
            id="city_group"
            label="City"
            label-for="city"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="city"
              v-model="client.city"
              type="text"
              required
            ></b-form-input>
          </b-form-group>

          <b-form-group
            id="state_group"
            label="State"
            label-for="state"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-select
              id="state"
              v-model="client.state"
              :options="stateOptions"
              required
            ></b-form-select>
          </b-form-group>

          <b-form-group
            id="zip_code_group"
            label="Zip code"
            label-for="zip_code"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="zip_code"
              v-model="client.zip_code"
              type="text"
              required
            ></b-form-input>
          </b-form-group>

          <b-form-group
            id="phone_number_group"
            label="Phone number"
            label-for="phone_number"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="phone_number"
              v-model="client.phone_number"
              type="tel"
              required
            ></b-form-input>
          </b-form-group>

          <b-form-group
            id="email_group"
            label="Email"
            label-for="email"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="email"
              v-model="client.email"
              type="email"
              required
            ></b-form-input>
          </b-form-group>

          <b-form-group
            id="referrer_group"
            label="Referrer"
            label-for="referrer"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-select
              id="referrer"
              v-model="client.referrer_id"
              :options="referrerOptions"
              required
            ></b-form-select>
          </b-form-group>
          <div class="text-right">
            <b-button @click="cancel" variant="link">Cancel</b-button>
            <b-button type="submit" variant="primary">Save</b-button>
          </div>
        </b-form>
      </div>
    </div>
  </div>
</template>

<script>
import Backend from '@/backend';
import {formatErrors} from "@/utils";
export default {
  name: "ClientForm",
  props: {
    id: Number,
  },
  data() {
    return {
      client: {},
      referrerOptions: [],
      suffixOptions: ["", "Jr", "Sr", "II", "III"],
      stateOptions: [
          'AL',
          'AK',
          'AZ',
          'AR',
          'CA',
          'CO',
          'CT',
          'DE',
          'DC',
          'FL',
          'GA',
          'HI',
          'ID',
          'IL',
          'IN',
          'IA',
          'KS',
          'KY',
          'LA',
          'ME',
          'MD',
          'MA',
          'MI',
          'MN',
          'MS',
          'MO',
          'MT',
          'NE',
          'NV',
          'NH',
          'NJ',
          'NM',
          'NY',
          'NC',
          'ND',
          'OH',
          'OK',
          'OR',
          'PA',
          'RI',
          'SC',
          'SD',
          'TN',
          'TX',
          'UT',
          'VT',
          'VA',
          'WA',
          'WV',
          'WI',
          'WY',
      ]
    }
  },
  async created() {
    if (this.id) {
      this.getClient();
    }
    this.getReferrerOptions();
  },
  methods: {
    title() {
      return this.persisted() ? "Edit Client" : "Add Client"
    },
    persisted() {
      return this.client && this.client.id != null
    },
    getClient() {
      Backend.getClient(this.id).then((client) => {
        this.client = client;
      });
    },
    getReferrerOptions() {
      Backend.getReferrers().then((referrers) => {
        referrers.forEach(referrer => {
          this.referrerOptions.push({ value: referrer.id, text: referrer.name });
        })
      })
    },
    cancel() {
      if (this.persisted()) {
        this.$router.push({ name: 'client', params: { id: String(this.id) } });
      } else {
        this.$router.push({ name: 'clients' });
      }
    },
    save() {
      if (this.persisted()) {
        Backend.updateClient(this.client.id, this.client).then(() => {
          this.$store.commit("setAlertSuccess", "Client updated successfully.");
          this.$router.push({ name: 'clients' });
        }).catch((errors) => {
          this.$store.commit("setAlertError", formatErrors("Client could not be saved", errors));
        });
      } else {
        Backend.createClient(this.client).then(() => {
          this.$store.commit("setAlertSuccess", "Client created successfully.");
          this.$router.push({ name: 'clients' });
        }).catch((errors) => {
          this.$store.commit("setAlertError", formatErrors("Client could not be saved", errors));
        });
      }
    }
  }
}
</script>

