<template>
  <div class="container">
    <div class="row" v-if="isLoading">
      <b-spinner variant="primary" label="Spinning"></b-spinner>
    </div>
    <div class="row" v-if="!isLoading">
      <div class="col-lg-10">
        <h1>{{ user.name }}</h1>
      </div>
      <div class="col-lg-2">
        <b-button :to="{ name: 'edit_user', params: { id: user.id } }" variant="light">Edit</b-button>
        <b-button @click="deleteUser" variant="danger">Delete</b-button>
      </div>
    </div>
    <div class="row" v-if="!isLoading">
      <div class="col-lg">
        <p>{{ user.email }}</p>
      </div>
    </div>

    <hr/>

    <div class="row mt-3">
      <div class="col-lg">
        <h3>Tasks</h3>
      </div>
    </div>

    <div class="row mt-3">
      <div class="col-lg">
        <task-table :tasks="tasks" exclude-staff />
      </div>
    </div>
  </div>
</template>

<script>
import Backend from "@/backend";
import {User} from "@/user";
import {formatErrors} from "@/utils";
import TaskTable from "@/components/TaskTable.vue";

export default {
  name: "UserView",
  components: {
    'task-table': TaskTable,
  },
  props: {
    id: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      user: null,
      isLoading: true,
      tasks: [],
    }
  },
  created() {
    if (!User.canModifyUsers()) {
      this.$router.push({ name: 'home' });
      this.$store.commit("setAlertError", "You do not have permission to access that page.");
    }
    this.getUser();
  },
  methods: {
    getUser() {
      Backend.getUser(this.id).then((user) => {
        this.user = user;
        this.isLoading = false;
        this.tasks = user.tasks;
      }).catch((statusCode) => {
        if (statusCode === 404) {
          this.$router.push({ name: 'users' });
          this.$store.commit("setAlertError", "User not found.");
        } else {
          console.log(statusCode);
        }
      });
    },
    deleteUser() {
      if (confirm("Are you sure? This action cannot be undone.")) {
        Backend.deleteUser(this.id).then(() => {
          this.$router.push({ name: 'users' });
          this.$store.commit("setAlertSuccess", "User was successfully deleted.");
        }).catch(errors => {
          this.$store.commit("setAlertError", formatErrors("User could not be deleted", errors));
        });
      }
    },
  }
}
</script>

