<template>
  <div class="container">
    <div class="row my-3">
      <div class="col-lg-11">
        <h1>Users</h1>
      </div>
      <div class="col-lg">
        <b-button variant="primary" :to="{ 'name': 'new_user' }">New</b-button>
      </div>
    </div>
    <div class="row">
      <div class="col-lg">
        <b-input v-model="filter" type="text" placeholder="Type to filter"/>
        <b-table
            id="user-table"
            bordered
            striped
            show-empty
            responsive
            :items="users"
            :fields="fields"
            sort-by="name"
            :filter="filter"
            :filter-included-fields="['name', 'email']"
            :current-page="currentPage"
            :per-page="pageSize"
            @filtered="filtered"
        >
          <template #cell(name)="data">
            <router-link :to="{ name: 'user', params: { id: data.item.id }}">{{ data.item.name }}</router-link>
          </template>
          <template #cell(can_modify_users)="data">
            <b-icon-check-circle-fill variant="success" v-if="data.item.can_modify_users"></b-icon-check-circle-fill>
            <b-icon-x-square-fill variant="danger" v-else></b-icon-x-square-fill>
          </template>
        </b-table>

        <div class="overflow-auto">
          <b-pagination
              v-model="currentPage"
              :total-rows="rowCount"
              :per-page="pageSize"
              aria-controls="user-table"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Backend from "@/backend";
import {User} from "@/user";
import {toLocalTime} from "@/convert_date";

export default {
  name: "UserList",
  data() {
    return {
      filter: null,
      fields: [
        'name',
        'email',
        'can_modify_users',
        { key: 'created', formatter: toLocalTime },
        { key: 'updated', formatter: toLocalTime },
      ],
      users: [],
      pageSize: 10,
      currentPage: 1,
      rowCount: null,
    }
  },
  created() {
    if (!User.canModifyUsers()) {
      this.$router.push({ name: 'home' });
      this.$store.commit("setAlertError", "You do not have permission to access that page.");
    }
    this.loadUsers();
  },
  methods: {
    loadUsers() {
      Backend.getUsers().then((users) => {
        this.users = users;
        this.rowCount = users.length;
      })
    },
    filtered(items) {
      this.rowCount = items.length;
      this.currentPage = 1;
    },
  }
}
</script>

