<template>
  <div>
    <b-input v-model="filter" type="text" placeholder="Type to filter"/>
    <b-table
        id="task-table"
        bordered
        striped
        show-empty
        responsive
        :items="tasks"
        :fields="fields"
        :filter="filter"
        sort-by="id"
        :filter-included-fields="['status', 'name', 'description', 'staff_name', 'matter_name']"
        :per-page="pageSize"
        :current-page="currentPage"
        @filtered="filtered"
    >
      <template #cell(actions)="data">
        <b-button-group v-if="data.item.status === 'Incomplete'">
          <b-button @click="completeTask(data.item.id)" size="sm" variant="success">Complete</b-button>
          <b-button @click="cancelTask(data.item.id)" size="sm" variant="danger">Cancel</b-button>
        </b-button-group>
        <div v-else>n/a</div>
      </template>
      <template #cell(matter_name)="data">
        <router-link :to="{ name: 'matter', params: { id: data.item.id } }">{{ data.item.matter_name }}</router-link>
      </template>
    </b-table>

    <div class="overflow-auto">
      <b-pagination
          v-model="currentPage"
          :total-rows="rowCount"
          :per-page="pageSize"
          aria-controls="task-table"
      />
    </div>
  </div>
</template>

<script>
import {toLocalTime} from "@/convert_date";
import Backend from "@/backend";
import _ from "lodash";

export default {
  name: "TaskTable",
  props: {
    tasks: {
      type: Array,
      required: true,
    },
    excludeMatter: {
      type: Boolean,
      default: false,
    },
    excludeStaff: {
      type: Boolean,
      default: false,
    },
    pageSize: {
      type: Number,
      default: 1,
    },
  },
  data() {
    return {
      filter: null,
      currentPage: 1,
      rowCount: null,
    };
  },
  computed: {
    fields() {
      let f = [
        'status',
        'name',
        'description',
        { key: 'created', formatter: toLocalTime },
        { key: 'updated', formatter: toLocalTime },
        'actions',
      ];

      if (!this.excludeStaff) {
        f.splice(3, 0, { key: 'staff_name', label: 'Staff' });
      }

      if (!this.excludeMatter) {
        f.splice(3, 0, { key: 'matter_name', label: 'Matter'});
      }

      return f;
    },
  },
  updated() {
    if (this.rowCount == null) {
      this.rowCount = this.tasks.length;
    }
  },
  methods: {
    completeTask(taskId) {
      let task = _.find(this.tasks, {id: taskId});
      task.status = "Completed";
      Backend.updateTask(taskId, task).then(() => {
        this.$store.commit("setAlertSuccess", "Task completed.")
      }).catch(() => {
        this.$store.commit("setAlertError", "Task could not be completed.")
      })
    },
    cancelTask(taskId) {
      Backend.deleteTask(taskId).then(() => {
        this.tasks = _.reject(this.tasks, {id: taskId});
        this.$store.commit("setAlertSuccess", "Task cancelled.")
      }).catch(() => {
        this.$store.commit("setAlertError", "Task could not be cancelled.")
      })
    },
    filtered(items) {
      this.rowCount = items.length;
      this.currentPage = 1;
    },
  }
}
</script>

