<template>
  <div class="container">
    <div class="row my-3">
      <div class="col-lg">
        <h1>{{ title() }}</h1>
      </div>
    </div>

    <div class="row">
      <div class="col-lg">
        <b-form @submit="save">
          <b-form-group
            id="title_group"
            label="Title"
            label-for="title"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="title"
              v-model="matter.title"
              type="text"
              required
            >
            </b-form-input>
          </b-form-group>

          <b-form-group
            id="client_group"
            label="Client"
            label-for="client"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-select
              id="client"
              v-model="matter.client_id"
              :options="clientOptions"
              required
            ></b-form-select>
          </b-form-group>

          <b-form-group
            id="status_group"
            label="Status"
            label-for="status"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-select
              id="status"
              v-model="matter.status"
              :options="statusOptions"
              required
            ></b-form-select>
          </b-form-group>

          <b-form-group
            id="workflow_group"
            label="Workflow"
            label-for="workflow"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-select
              id="workflow"
              v-model="matter.workflow_id"
              :options="workflowOptions"
            ></b-form-select>
          </b-form-group>

          <div class="text-right">
            <b-button @click="cancel" variant="link">Cancel</b-button>
            <b-button type="submit" variant="primary">Save</b-button>
          </div>
        </b-form>
      </div>
    </div>
  </div>
</template>

<script>
import Backend from "@/backend";
import {formatErrors, fullName} from "@/utils";

export default {
  name: "MatterForm",
  props: {
    id: Number,
    clientId: Number,
  },
  data() {
    return {
      matter: {},
      clientOptions: [],
      workflowOptions: [],
      statusOptions: [
          "Prospect",
          "Open",
          "Closed",
      ],
    };
  },
  async created() {
    if (this.id) {
      this.getMatter();
    }
    this.getClientOptions();
    this.getWorkflowOptions();
    if (this.clientId) {
      this.matter.client_id = this.clientId;
    }
  },
  methods: {
    getMatter() {
      Backend.getMatter(this.id).then(matter => {
        this.matter = matter;
      }).catch((statusCode) => {
        if (statusCode === 404) {
          this.$router.push({ name: 'matters' });
          this.$store.commit("setAlertError", "Matter not found.");
          return;
        }
        console.log(statusCode);
      });
    },
    getClientOptions() {
      Backend.getClients().then((clients) => {
        clients.forEach(client => {
          this.clientOptions.push({value: client.id, text: fullName(client)})
        })
      }).catch(() => {
        this.$store.commit("setAlertError", "Error loading client options.");
      });
    },
    getWorkflowOptions() {
      this.workflowOptions = [{value: null, text: "-- none --"}]
      Backend.getWorkflows().then(workflows => {
        workflows.forEach(workflow => {
          this.workflowOptions.push({value: workflow.id, text: workflow.name})
        })
      }).catch(() => {
        this.$store.commit("setAlertError", "Error loading client options.");
      });
    },
    title() {
      return this.persisted() ? "Edit Matter" : "Add Matter";
    },
    persisted() {
      return this.id != null;
    },
    cancel() {
      if (this.persisted()) {
        this.$router.push({ name: 'matter', params: { id: String(this.id) } });
      } else {
        this.$router.push({ name: 'matters' });
      }
    },
    save() {
      if (this.persisted()) {
        Backend.updateMatter(this.id, this.matter).then(() => {
          this.$router.push({ name: 'matter', params: { id: String(this.id) } });
          this.$store.commit("setAlertSuccess", "Matter updated successfully.");
        }).catch((errors) => {
          this.$store.commit("setAlertError", formatErrors("Matter could not be saved", errors));
        });
      } else {
        Backend.createMatter(this.matter).then(() => {
          this.$router.push({ name: 'matters' });
          this.$store.commit("setAlertSuccess", "Matter created successfully.");
        }).catch((errors) => {
          this.$store.commit("setAlertError", formatErrors("Matter could not be saved", errors));
        });
      }
    }
  }
}
</script>

