<template>
  <div class="container">
    <div class="row my-3">
      <div class="col-lg-11">
        <h1>Matters</h1>
      </div>
      <div class="col-lg">
        <b-button variant="primary" :to="{ 'name': 'new_matter' }">New</b-button>
      </div>
    </div>
    <div class="row">
      <div class="col-lg">
        <matter-table :matters="matters"></matter-table>
      </div>
    </div>
  </div>
</template>

<script>
import MatterTable from "@/components/MatterTable.vue";
import Backend from "@/backend";

export default {
  name: "MatterList",
  components: {
    "matter-table": MatterTable,
  },
  data() {
    return {
      matters: [],
    }
  },
  async created() {
    this.getMatters();
  },
  methods: {
    getMatters() {
      Backend.getMatters().then((matters) => {
        this.matters = matters;
      }).catch(() => {
        this.$store.commit("setAlertError", "Error retrieving list of matters");
      });
    }
  }
}
</script>

