<template>
  <div class="container">
    <div class="row my-3">
      <div class="col-lg">
        <h1>{{ title() }}</h1>
      </div>
    </div>

    <div class="row">
      <div class="col-lg">
        <b-form @submit="save">
          <b-form-group
            id="workflow_group"
            label="Workflow"
            label-for="workflow"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-select
              id="workflow"
              v-model="step.workflow_id"
              :options="workflowOptions"
              disabled
              required
            ></b-form-select>
          </b-form-group>

          <b-form-group
            id="name_group"
            label="Name"
            label-for="name"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="name"
              v-model="step.name"
              type="text"
              required
            >
            </b-form-input>
          </b-form-group>

          <b-form-group
            id="order_group"
            label="Order"
            label-for="order"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="order"
              v-model="step.order"
              type="number"
              min="1"
              step="1"
              required
            >
            </b-form-input>
          </b-form-group>

          <b-form-group
            id="description_group"
            label="Description"
            label-for="description"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-textarea
              id="textarea"
              v-model="step.description"
              rows="5"
              max-rows="5"
            />
          </b-form-group>

          <div class="text-right">
            <b-button @click="cancel" variant="link">Cancel</b-button>
            <b-button type="submit" variant="primary">Save</b-button>
          </div>
        </b-form>
      </div>
    </div>
  </div>
</template>

<script>
import Backend from "@/backend";
import {formatErrors} from "@/utils";

export default {
  name: "StepForm",
  props: {
    id: Number,
    workflowId: Number,
  },
  data() {
    return {
      step: {},
      workflowOptions: [],
      userOptions: [],
    };
  },
  async created() {
    if (this.id) {
      this.getStep();
    }
    this.getWorkflowOptions();
    this.getUserOptions();
  },
  methods: {
    title() {
      return this.persisted() ? "Edit Step" : "Add Step";
    },
    persisted() {
      return this.id != null;
    },
    getStep() {
      Backend.getStep(this.id).then(step => {
        this.step = step;
      }).catch(statusCode => {
        if (statusCode === 404) {
          this.$router.push({ name: 'workflow', params: { id: String(this.workflowId) }});
          this.$store.commit("setAlertError", "Step not found.");
        } else {
          console.log(statusCode);
        }
      });
    },
    getWorkflowOptions() {
      Backend.getWorkflows().then(workflows => {
        workflows.forEach(workflow => {
          this.workflowOptions.push({ value: workflow.id, text: workflow.name });
        });
        if (this.workflowId != null) {
          this.step.workflow_id = this.workflowId;
        }
      }).catch(statusCode => {
        if (statusCode === 404) {
          this.$router.push({ name: 'workflows' });
          this.$store.commit("setAlertError", "Workflow not found.");
        } else {
          console.log(statusCode);
        }
      });
    },
    getUserOptions() {
      Backend.getUsers().then(users => {
        users.forEach(user => {
          this.userOptions.push({ value: user.id, text: user.name });
        });
      }).catch(statusCode => {
        if (statusCode === 404) {
          this.$store.commit("setAlertError", "Error retrieving user options.");
        } else {
          console.log(statusCode);
        }
      });
    },
    cancel() {
      this.$router.push({ name: 'workflow', params: { id: String(this.step.workflow_id) } });
    },
    save() {
      if (this.persisted()) {
        Backend.updateStep(this.id, this.step).then(() => {
          this.$router.push({ name: 'workflow', params: { id: String(this.step.workflow_id) } });
          this.$store.commit("setAlertSuccess", "Step updated successfully.");
        }).catch((errors) => {
          this.$store.commit("setAlertError", formatErrors("Step could not be saved", errors));
        });
      } else {
        Backend.createStep(this.step).then(() => {
          this.$router.push({ name: 'workflow', params: { id: String(this.step.workflow_id) } });
          this.$store.commit("setAlertSuccess", "Step created successfully.");
        }).catch((errors) => {
          this.$store.commit("setAlertError", formatErrors("Step could not be saved", errors));
        });
      }
    }
  }
}
</script>

