import {store} from "@/store";

export const User = {
    getToken() {
        return store.getters.getToken;
    },
    getUserId() {
        return store.getters.getUserId;
    },
    login(token, user) {
        store.commit("setToken", token);
        store.commit("setUserId", user.id);
        store.commit("setUserName", user.name);
        store.commit("setUserEmail", user.email);
        store.commit("setUserCanModifyUsers", user.can_modify_users);
    },
    logout() {
        store.commit("logout");
    },
    isLoggedIn() {
        return this.getToken() != null;
    },
    canModifyUsers() {
        return store.getters.getUserCanModifyUsers;
    },
    loginExpired() {
        const token = getJwt(this.getToken())
        return token.exp < Date.now() / 1000
    }
}

export const getJwt = (token) => {
    const base64 = token.split('.')[1].replace(/-/g, '+').replace(/_/g, '/');
    const jsonPayload = decodeURIComponent(Buffer.from(base64, "base64").toString("ascii").split("").map(function(c) {
        return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
    }).join(''));

    return JSON.parse(jsonPayload);
}