<template>
  <div class="container">
    <div class="row my-3">
      <div class="col-lg-11">
        <h1>Appointments</h1>
      </div>
      <div class="col-lg">
        <b-button variant="primary" :to="{ 'name': 'new_appointment' }">New</b-button>
      </div>
    </div>
    <div class="row">
      <div class="col-lg">
        <appointment-table :appointments="appointments"></appointment-table>
      </div>
    </div>
  </div>
</template>

<script>
import AppointmentTable from "@/components/AppointmentTable.vue";
import Backend from "@/backend";

export default {
  name: "AppointmentList",
  components: {
    "appointment-table": AppointmentTable,
  },
  data() {
    return {
      appointments: [],
    };
  },
  async created() {
    this.getAppointments();
  },
  methods: {
    getAppointments() {
      Backend.getAppointments().then(appointments => {
        this.appointments = appointments;
      }).catch(() => {
        this.$store.commit("setAlertError", "Error retrieving list of appointments");
      });
    }
  }
}
</script>

