<template>
    <div class="container">
    <div class="row" v-if="isLoading">
      <b-spinner variant="primary" label="Spinning"></b-spinner>
    </div>
    <div class="row my-3" v-if="!isLoading">
      <div class="col-lg-10">
        <h1>{{ appointment.title }}</h1>
      </div>
      <div class="col-lg-2 text-right">
        <b-button :to="{ name: 'edit_appointment', params: { id: appointment.id } }" variant="light">Edit</b-button>
        <b-button @click="deleteAppointment" variant="danger">Delete</b-button>
      </div>
    </div>
    <div class="row" v-if="!isLoading">
      <div class="col-lg">
        <dl>
          <dt>Matter</dt>
          <dd>{{ appointment.client_name }} - {{ appointment.matter_title }}</dd>
          <dt>Staff</dt>
          <dd>{{ appointment.staff_name }}</dd>
          <dt>Start</dt>
          <dd>{{ toLocalTime(appointment.start) }}</dd>
          <dt>End</dt>
          <dd>{{ toLocalTime(appointment.end) }}</dd>
          <dt>Description</dt>
          <dd>{{ appointment.description }}</dd>
          <dt>Location</dt>
          <dd>{{ appointment.location }}</dd>
          <dt>Created</dt>
          <dd>{{ toLocalTime(appointment.created) }}</dd>
          <dt>Updated</dt>
          <dd>{{ toLocalTime(appointment.updated) }}</dd>
        </dl>
      </div>
    </div>
  </div>
</template>

<script>
import Backend from '@/backend';
import {toLocalTime} from "../convert_date";
import {formatErrors} from "@/utils";

export default {
  name: "AppointmentView",
  props: {
    id: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      appointment: null,
      isLoading: true,
    };
  },
  async created() {
    this.getAppointment();
  },
  methods: {
    toLocalTime,
    getAppointment() {
      Backend.getAppointment(this.id).then(appt => {
        this.appointment = appt;
        this.isLoading = false;
      }).catch(statusCode => {
        if (statusCode === 404) {
          this.$router.push({name: 'appointments'});
          this.$store.commit("setAlertError", "Appointment not found.");
        } else {
          console.log(statusCode);
        }
      });
    },
    deleteAppointment() {
      if (confirm("Are you sure? This action cannot be undone.")) {
        Backend.deleteAppointment(this.id).then(() => {
          this.$router.push({name: 'appointments'});
          this.$store.commit("setAlertSuccess", "Appointment deleted successfully.");
        }).catch(errors => {
          this.$store.commit("setAlertError", formatErrors("Appointment could not be deleted", errors));
        });
      }
    }
  }
}
</script>

