<template>
  <div class="container">
    <div class="row my-3">
      <div class="col-lg">
        <h1>Referrer Report</h1>
      </div>
    </div>

    <div class="row">
      <div class="col-lg">
        <b-table bordered striped show-empty responsive :items="referrerReportRecords" :fields="fields" sort-by="count" sort-desc>
          <template #cell(percentage)="data">
            {{ percentage(data.item.count) }}
          </template>
        </b-table>
      </div>
    </div>
  </div>
</template>

<script>
import Backend from "@/backend";
import _ from 'lodash';

export default {
  name: "ReferrerReport",
  data() {
    return {
      referrerReportRecords: [],
      fields: [
        { key: "name", label: "Referrer" },
        'count',
        'percentage',
      ]
    };
  },
  async created() {
    this.getReferrerReportRecords();
  },
  methods: {
    getReferrerReportRecords() {
      Backend.getReport("referrer").then(referrerReportRecords => {
        this.referrerReportRecords = referrerReportRecords;
      }).catch(statusCode => {
        console.log(statusCode);
      });
    },
    percentage(amount) {
      const total = _.reduce(this.referrerReportRecords, (sum, n) => {
        return sum + n.count;
      }, 0);
      return `${amount / total * 100}%`;
    }
  }
}
</script>

