<template>
  <div class="container">
    <div class="row mt-3">
      <div class="col-lg">
        <h1>Login</h1>
      </div>
    </div>
    <div class="row">
      <div class="col-lg">
        <b-form @submit="login">
          <b-form-group
            id="email_group"
            label="Email"
            label-for="email"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="email"
              v-model="attempt.email"
              type="text"
              required
            ></b-form-input>
          </b-form-group>

          <b-form-group
            id="password_group"
            label="Password"
            label-for="password"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="password"
              v-model="attempt.password"
              type="password"
              required
            ></b-form-input>
          </b-form-group>
          <b-button type="submit" variant="primary">Submit</b-button>
        </b-form>
      </div>
    </div>
  </div>
</template>

<script>
import { User } from "@/user";
import Backend from "@/backend";

export default {
  name: "Login",
  data() {
    return {
      attempt: {},
    }
  },
  methods: {
    login() {
      this.error = ""
      Backend.login(this.attempt.email, this.attempt.password).then((resp) => {
        User.login(resp.token, resp.user);
        this.$store.commit("setAlertSuccess", "Login was successful.");
        this.$router.push({ name: 'home' });
      }).catch((statusCode) => {
        if (statusCode === 401) {
          this.$store.commit("setAlertError", "Invalid email or password.");
        } else {
          console.log(statusCode);
        }
      });
    }
  }
}
</script>

