<template>
    <div class="container">
      <div class="row my-3">
        <div class="col-lg">
          <h1>{{ title() }}</h1>
        </div>
      </div>

      <div class="row">
        <div class="col-lg">
          <b-form @submit="save">
            <b-form-group
              id="name_group"
              label="Name"
              label-for="name"
              label-cols-lg="3"
              content-cols-lg="9"
            >
              <b-form-input
                id="name"
                v-model="user.name"
                type="text"
                required
              ></b-form-input>
            </b-form-group>

            <b-form-group
              id="email_group"
              label="Email"
              label-for="email"
              label-cols-lg="3"
              content-cols-lg="9"
            >
              <b-form-input
                id="email"
                v-model="user.email"
                type="email"
                required
              ></b-form-input>
            </b-form-group>

            <b-form-group
              id="password_group"
              label="Password"
              label-for="password"
              label-cols-lg="3"
              content-cols-lg="9"
            >
              <b-form-input
                id="password"
                v-model="user.password"
                type="password"
                :required="!persisted()"
              ></b-form-input>
            </b-form-group>

            <b-form-group
              id="can_modify_users_group"
              label-cols-lg="3"
              content-cols-lg="9"
            >
              <b-form-checkbox
                id="can_modify_users"
                v-model="user.can_modify_users"
              >
                Can modify users
              </b-form-checkbox>
            </b-form-group>

            <div class="text-right">
              <b-button @click="cancel" variant="link">Cancel</b-button>
              <b-button type="submit" variant="primary">Save</b-button>
            </div>
          </b-form>
        </div>
      </div>
    </div>
</template>

<script>
import Backend from "@/backend";
import {User} from "@/user";
import {formatErrors} from "@/utils";

export default {
  name: "UserForm",
  props: {
    id: Number,
  },
  data() {
    return {
      user: {},
    }
  },
  created() {
    if (!User.canModifyUsers()) {
      this.$router.push({ name: 'home' });
      this.$store.commit("setAlertError", "You do not have permission to access that page.");
      return;
    }
    if (this.id) {
      this.getUser();
    }
  },
  methods: {
    title() {
      return this.persisted() ? "Edit User" : "Add User"
    },
    persisted() {
      return this.user.id != null
    },
    getUser() {
      Backend.getUser(this.id).then(user => {
        this.user = user;
      }).catch((resp) => {
        if (resp.statusCode === 404) {
          this.$store.commit("setAlertError", "User not found.")
          this.$router.push({ name: 'users' });
          return;
        }
        console.log(resp.statusCode);
      });
    },
    cancel() {
      if (this.persisted()) {
        this.$router.push({ name: 'user', params: { id: String(this.id) } });
      } else {
        this.$router.push({ name: 'users' });
      }
    },
    save() {
      if (this.persisted()) {
        Backend.updateUser(this.id, this.user).then(() => {
          this.$store.commit("setAlertSuccess", "User updated successfully.")
          this.$router.push({ name: 'users' });
        }).catch((errors) => {
          this.$store.commit("setAlertError", formatErrors("User could not be saved", errors));
        });
      } else {
        Backend.createUser(this.user).then(() => {
          this.$store.commit("setAlertSuccess", "User created successfully.")
          this.$router.push({ name: 'users' });
        }).catch((errors) => {
          this.$store.commit("setAlertError", formatErrors("User could not be saved", errors));
        });
      }
    }
  },
}
</script>

