<template>
  <div class="container">
    <div class="row" v-if="isLoading">
      <b-spinner variant="primary" label="Spinning"></b-spinner>
    </div>
    <div class="row my-3" v-if="!isLoading">
      <div class="col-lg-10">
        <h1>Transaction #{{ transaction.id }}</h1>
      </div>
      <div class="col-lg-2 text-right">
        <b-button :to="{ name: 'edit_transaction', params: { id: transaction.id } }" variant="light">Edit</b-button>
        <b-button @click="deleteTransaction" variant="danger">Delete</b-button>
      </div>
    </div>
    <div class="row" v-if="!isLoading">
      <div class="col-lg">
        <dl>
          <dt>Description</dt>
          <dd>{{ transaction.description }}</dd>
          <dt>Amount</dt>
          <dd>{{ formatMoney(transaction.amount) }}</dd>
          <dt>Created</dt>
          <dd>{{ toLocalTime(transaction.created) }}</dd>
          <dt>Updated</dt>
          <dd>{{ toLocalTime(transaction.updated) }}</dd>
        </dl>
      </div>
    </div>
  </div>
</template>

<script>
import {Money} from "../money";
import Backend from "@/backend";
import {toLocalTime} from "../convert_date";
import {formatErrors} from "@/utils";

export default {
  name: "TransactionView",
  props: {
    id: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      transaction: null,
      isLoading: true,
    }
  },
  async created() {
    this.getTransaction();
  },
  methods: {
    toLocalTime,
    formatMoney(text) {
      return Money.format(text);
    },
    getTransaction() {
      Backend.getTransaction(this.id).then((txn) => {
        this.transaction = txn;
        this.isLoading = false;
      }).catch(statusCode => {
        if (statusCode === 404) {
          this.$router.push({'name': 'transactions'});
          this.$store.commit("setAlertError", "Transaction not found.");
        } else {
          console.log(statusCode);
        }
      })
    },
    deleteTransaction() {
      if (confirm("Are you sure? This action cannot be undone.")) {
        Backend.deleteTransaction(this.id).then(() => {
          this.$router.push({name: 'transactions'});
          this.$store.commit("setAlertSuccess", "Transaction deleted successfully.");
        }).catch(errors => {
          this.$store.commit("setAlertError", formatErrors("Transaction could not be deleted", errors));
        });
      }
    }
  }
}
</script>

