<template>
  <div class="container">
    <div class="row my-3">
      <div class="col-lg-11">
        <h1>Referrers</h1>
      </div>
      <div class="col-lg">
        <b-button variant="primary" :to="{ 'name': 'new_referrer' }">New</b-button>
      </div>
    </div>

    <div class="row">
      <div class="col-lg">
        <div>
          <b-input v-model="filter" type="text" placeholder="Type to filter"/>
          <b-table
              id="referrer-table"
              bordered
              striped
              show-empty
              responsive
              :items="referrers"
              :fields="fields"
              sort-by="name"
              :filter="filter"
              :filter-included-fields="['name']"
              :per-page="pageSize"
              :current-page="currentPage"
              @filtered="filtered"
          >
            <template #cell(name)="data">
              <router-link :to="{ name: 'referrer', params: { id: data.item.id }}">{{ data.item.name }}</router-link>
            </template>
          </b-table>
          <div class="overflow-auto">
            <b-pagination
              v-model="currentPage"
              :total-rows="rowCount"
              :per-page="pageSize"
              aria-controls="referrer-table"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Backend from '@/backend';
import {toLocalTime} from "@/convert_date";
export default {
  name: "ReferrerList",
  data() {
    return {
      filter: null,
      fields: [
        'name',
        { key: 'created', formatter: toLocalTime },
        { key: 'updated', formatter: toLocalTime },
      ],
      referrers: [],
      currentPage: 1,
      rowCount: null,
      pageSize: 10,
    }
  },
  async created() {
    this.loadReferrers();
  },
  methods: {
    loadReferrers() {
      Backend.getReferrers().then((referrers) => {
        this.referrers = referrers;
        this.rowCount = this.referrers.length;
      })
    },
    filtered(items) {
      this.rowCount = items.length;
      this.currentPage = 1;
    },
  }
}
</script>

