import superagent from "superagent";
import { router } from "@/main";
import {User} from "@/user";
import _ from "lodash";

const API_PREFIX = "https://gd-c868-be.herokuapp.com/api"

const checkLoggedIn = () => {
    if (!User.isLoggedIn() || User.loginExpired()) {
        router.push({name: 'login', params: { error: 'Please login'}});
        throw "Not logged in";
    }
}

class Resource {
    constructor(resource, overridePlural, filters) {
        this.resource = resource;
        this.resource_path = API_PREFIX + "/" + resource + "/";
        if (overridePlural != null) {
            this.overridePlural = overridePlural;
        }

        this.include = {
            create: true,
            get: true,
            list: true,
            update: true,
            delete: true,
        }

        if (filters == null) {
            return;
        }

        if (filters.except != null && filters.only != null) {
            throw "Specify either except or only, but not both";
        }

        if (filters.only != null) {
            _.keys(this.include).forEach(endpoint => {
                this.include[endpoint] = filters.only.includes(endpoint)
            })
        }

        if (filters.except != null) {
            filters.except.forEach(endpoint => {
                this.include[endpoint] = false;
            });
        }
    }

    endpoints() {
        const res = _.upperFirst(_.camelCase(this.resource));
        let pluralRes = res + "s";
        if (this.overridePlural != null) {
            pluralRes = this.overridePlural;
        }

        const output = {};
        if (this.include.create) {
            output["create" + res] = this.createEndpoint();
        }
        if (this.include.get) {
            output["get" + res] = this.getEndpoint();
        }
        if (this.include.list) {
            output["get" + pluralRes] = this.listEndpoint();
        }
        if (this.include.delete) {
            output["delete" + res] = this.deleteEndpoint();
        }
        if (this.include.update) {
            output["update" + res] = this.updateEndpoint();
        }

        return output;
    }

    createEndpoint() {
        return (obj) => {
            checkLoggedIn();
            return new Promise((resolve, reject) => {
               superagent
                   .post(this.resource_path)
                   .set("Authorization", "Bearer " + User.getToken())
                   .send(obj)
                   .end((err, res) => {
                       if (res.ok) {
                           resolve(res.body);
                       } else {
                           let messages = [];
                           res.body.detail.forEach(err => {
                               messages.push(err.msg);
                           });
                           reject(messages);
                       }
                   });
            });
        }
    }

    getEndpoint() {
        return (objId) => {
            checkLoggedIn();
            return new Promise((resolve, reject) => {
                superagent
                    .get(this.resource_path + objId)
                    .set("Authorization", "Bearer " + User.getToken())
                    .end((err, res) => {
                        if (res.ok) {
                            resolve(res.body);
                        } else {
                            reject(res.statusCode);
                        }
                    });
            });
        }
    }

    listEndpoint() {
        return () => {
            checkLoggedIn();
            return new Promise((resolve, reject) => {
                superagent
                    .get(this.resource_path)
                    .set("Authorization", "Bearer " + User.getToken())
                    .end((err, res) => {
                        if (res.ok) {
                            resolve(res.body);
                        } else {
                            reject(res.statusCode);
                        }
                    });
            });
        }
    }

    deleteEndpoint() {
        return (objId) => {
            checkLoggedIn();
            return new Promise((resolve, reject) => {
                superagent
                    .delete(this.resource_path + objId)
                    .set("Authorization", "Bearer " + User.getToken())
                    .end((err, res) => {
                        if (res.ok) {
                            resolve(res.body);
                        } else {
                           let messages = [];
                           res.body.detail.forEach(err => {
                               messages.push(err.msg);
                           });
                           reject(messages);
                        }
                    });
            });
        }
    }

    updateEndpoint() {
        return (objId, obj) => {
            checkLoggedIn();
            return new Promise((resolve, reject) => {
                superagent
                    .patch(this.resource_path + objId)
                    .set("Authorization", "Bearer " + User.getToken())
                    .send(obj)
                    .end((err, res) => {
                        if (res.ok) {
                            resolve(res.body);
                        } else {
                           let messages = [];
                           res.body.detail.forEach(err => {
                               messages.push(err.msg);
                           });
                           reject(messages);
                        }
                    });
            });
        }
    }
}


export default {
    login(email, password) {
        return new Promise((resolve, reject) => {
            superagent
                .post(API_PREFIX + "/login")
                .send({email, password})
                .end((err, res) => {
                    if (res.ok) {
                        resolve(res.body);
                    } else {
                        reject(res.statusCode);
                    }
                })
        });
    },
    ...new Resource("appointment").endpoints(),
    ...new Resource("client").endpoints(),
    ...new Resource("matter").endpoints(),
    ...new Resource("note", null, { only: ["create"] }).endpoints(),
    ...new Resource("referrer").endpoints(),
    ...new Resource("step", null, { except: ["list"] }).endpoints(),
    ...new Resource("task", null, { only: ["update", "delete"] }).endpoints(),
    ...new Resource("task_template", null, { except: ["list"] }).endpoints(),
    ...new Resource("transaction").endpoints(),
    ...new Resource("user").endpoints(),
    ...new Resource("workflow").endpoints(),
    getReport: (report) => {
        checkLoggedIn();
        return new Promise((resolve, reject) => {
            superagent
                .get(API_PREFIX + "/report/" + report)
                .set("Authorization", "Bearer " + User.getToken())
                .end((err, res) => {
                    if (res.ok) {
                        resolve(res.body);
                    } else {
                        reject(res.statusCode);
                    }
                });
        });
    },
}
