<template>
  <div>
    <b-input v-model="filter" type="text" placeholder="Type to filter"/>
    <b-table
        id="matter-table"
        bordered
        striped
        show-empty
        responsive
        :items="matters"
        :fields="fields"
        sort-by="id"
        :filter="filter"
        :filter-included-fields="['title', 'client_name', 'status', 'id']"
        :per-page="pageSize"
        :current-page="currentPage"
        @filtered="filtered"
    >
      <template #cell(title)="data">
        <router-link :to="{ name: 'matter', params: { id: data.item.id }}">{{ data.item.title }}</router-link>
      </template>
      <template #cell(client_id)="data">
        <router-link :to="{ name: 'client', params: { id: data.item.client_id }}">{{ data.item.client_name }}</router-link>
      </template>
    </b-table>

    <div class="overflow-auto">
      <b-pagination
          v-model="currentPage"
          :total-rows="rowCount"
          :per-page="pageSize"
          aria-controls="matter-table"
      />
    </div>
  </div>
</template>

<script>
import {toLocalTime} from "@/convert_date";

export default {
  name: "MatterTable",
    props: {
    matters: {
      type: Array,
      required: true,
    },
    pageSize: {
      type: Number,
      default: 10,
    }
  },
  data() {
    return {
      filter: null,
      fields: [
        { key: "id", label: "No." },
        "title",
        { key: "client_id", label: "Client" },
        "status",
        { key: 'created', formatter: toLocalTime },
        { key: 'updated', formatter: toLocalTime },
      ],
      currentPage: 1,
      rowCount: null,
    };
  },
  updated() {
    if (this.rowCount == null) {
      this.rowCount = this.matters.length;
    }
  },
  methods: {
    filtered(items) {
      this.rowCount = items.length;
      this.currentPage = 1;
    }
  }
}
</script>

