<template>
  <div>
    <b-input v-model="filter" type="text" placeholder="Type to filter"/>
    <b-table
        id="workflow-table"
        bordered
        striped
        show-empty
        responsive
        :items="workflows"
        :fields="fields"
        sort-by="name"
        :filter="filter"
        :filter-included-fields="['name', 'description']"
        :per-page="pageSize"
        :current-page="currentPage"
        @filtered="filtered"
    >
      <template #cell(name)="data">
        <router-link :to="{ name: 'workflow', params: { id: data.item.id } }">{{ data.item.name }}</router-link>
      </template>
    </b-table>
    <div class="overflow-auto">
      <b-pagination
          v-model="currentPage"
          :total-rows="rowCount"
          :per-page="pageSize"
          aria-controls="workflow-table"
      />
    </div>
  </div>
</template>

<script>
import {toLocalTime} from "@/convert_date";

export default {
  name: "WorkflowTable",
  props: {
    workflows: {
      type: Array,
      required: true,
    },
    pageSize: {
      type: Number,
      default: 10,
    }
  },
  data() {
    return {
      filter: null,
      fields: [
        'name',
        'description',
        { key: 'created', formatter: toLocalTime },
        { key: 'updated', formatter: toLocalTime },
      ],
      currentPage: 1,
      rowCount: null,
    };
  },
  updated() {
    if (this.rowCount == null) {
      this.rowCount = this.workflows.length;
    }
  },
  methods: {
    filtered(items) {
      this.rowCount = items.length;
      this.currentPage = 1;
    },
  }
}
</script>

