<template>
  <div class="container">
    <div class="row" v-if="isLoading">
      <b-spinner variant="primary" label="Spinning"></b-spinner>
    </div>
    <div class="row mt-3" v-if="!isLoading">
      <div class="col-lg-10">
        <h1>{{ name(client) }}</h1>
      </div>
      <div class="col-lg-2 text-right">
        <b-button :to="editLink" variant="light">Edit</b-button>
        <b-button @click="deleteClient" variant="danger">Delete</b-button>
      </div>
    </div>
    <div class="row" v-if="!isLoading">
      <div class="col-lg">
        <dl>
          <dt>Address</dt>
          <dd><address v-html="address(client)"/></dd>
          <dt>Phone</dt>
          <dd>{{ client.phone_number }}</dd>
          <dt>Email</dt>
          <dd>{{ client.email }}</dd>
        </dl>
      </div>
    </div>

    <hr/>

    <div class="row my-3" v-if="!isLoading">
      <div class="col-lg-11">
        <h3>Matters</h3>
      </div>
      <div class="col-lg-1">
        <b-button variant="primary" :to="{ 'name': 'new_matter', params: { 'clientId': this.id } }">New</b-button>
      </div>
    </div>
    <div class="row" v-if="!isLoading">
      <div class="col-lg">
        <matter-table :matters="client.matters"/>
      </div>
    </div>
  </div>
</template>

<script>
import Backend from "@/backend";
import MatterTable from "@/components/MatterTable.vue";
import {formatErrors} from "@/utils";

export default {
  name: "ClientView",
  components: {
    "matter-table": MatterTable,
  },
  props: {
    id: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      client: null,
      isLoading: true,
    }
  },
  created() {
    this.getClient();
  },
  computed: {
    editLink() {
      if (this.client == null) {
        return ""
      }
      return { name: 'edit_client', params: { id: this.client.id }}
    }
  },
  methods: {
    getClient() {
      Backend.getClient(this.id).then((client) => {
        this.client = client;
        this.isLoading = false;
      }).catch(resp => {
        if (resp.statusCode === 404) {
          this.$store.commit("setAlertError", "Client could not be found.")
          this.$router.push({ name: 'clients' });
          return;
        }
        console.log(resp.statusCode);
      });
    },
    deleteClient() {
      if (confirm("Are you sure? This action cannot be undone.")) {
        Backend.deleteClient(this.client.id).then(() => {
          this.$router.push({ name: 'clients' });
          this.$store.commit("setAlertSuccess", "Client was succesfully deleted.")
        }).catch(errors => {
          this.$store.commit("setAlertError", formatErrors("Client could not be deleted", errors));
        });
      }
    },
    name(client) {
      if (client == null) {
        return;
      }
      let output = client.first_name + " ";
      if (client.middle_name != null) {
        output += client.middle_name + " ";
      }
      output += client.last_name;
      if (client.suffix != null) {
        output += " " + client.suffix;
      }

      return output;
    },
    address(client) {
      if (client == null) {
        return;
      }

      let output = client.address_1 + "<br/>";
      if (client.address_2 != null) {
        output += client.address_2 + "<br/>";
      }
      output += client.city + ", " + client.state + " " + client.zip_code;

      return output;
    }
  }
}
</script>

