import Vue from 'vue';
import VueRouter from 'vue-router';
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import BootstrapVue, {BootstrapVueIcons} from 'bootstrap-vue'


import App from '@/App.vue'
import Home from '@/components/Home.vue'
import ClientList from '@/components/ClientList.vue'
import ClientForm from '@/components/ClientForm.vue'
import ClientView from '@/components/ClientView.vue'
import UserList from '@/components/UserList.vue'
import UserForm from '@/components/UserForm.vue'
import UserView from '@/components/UserView.vue'
import ReferrerList from '@/components/ReferrerList.vue';
import ReferrerForm from '@/components/ReferrerForm.vue';
import ReferrerView from '@/components/ReferrerView.vue';
import Login from '@/components/Login.vue';

import {User} from "@/user";
import {store} from "@/store";
import MatterList from "@/components/MatterList.vue";
import MatterForm from "@/components/MatterForm.vue";
import MatterView from "@/components/MatterView.vue";
import TransactionList from "@/components/TransactionList.vue";
import TransactionForm from "@/components/TransactionForm.vue";
import TransactionView from "@/components/TransactionView.vue";
import AppointmentList from "@/components/AppointmentList.vue";
import AppointmentForm from "@/components/AppointmentForm.vue";
import AppointmentView from "@/components/AppointmentView.vue";
import WorkflowList from "@/components/WorkflowList.vue";
import WorkflowForm from "@/components/WorkflowForm.vue";
import WorkflowView from "@/components/WorkflowView.vue";
import StepForm from "@/components/StepForm.vue";
import StepView from "@/components/StepView.vue";
import TaskTemplateForm from "@/components/TaskTemplateForm.vue";
import AuditReport from "@/components/reports/AuditReport.vue";
import ReferrerReport from "@/components/reports/ReferrerReport.vue";
import OutstandingBalancesReport
  from "@/components/reports/OutstandingBalancesReport.vue";

Vue.config.productionTip = false

Vue.use(BootstrapVue)
Vue.use(BootstrapVueIcons)
Vue.use(VueRouter)


const components = {
  appointment: {
    list: AppointmentList,
    form: AppointmentForm,
    view: AppointmentView,
  },
  client: {
    list: ClientList,
    form: ClientForm,
    view: ClientView,
  },
  matter: {
    list: MatterList,
    form: MatterForm,
    view: MatterView,
  },
  referrer: {
    list: ReferrerList,
    form: ReferrerForm,
    view: ReferrerView,
  },
  step: {
    list: null,
    form: StepForm,
    view: StepView,
  },
  task_template: {
    list: null,
    form: TaskTemplateForm,
    view: null,
  },
  transaction: {
    list: TransactionList,
    form: TransactionForm,
    view: TransactionView,
  },
  user: {
    list: UserList,
    form: UserForm,
    view: UserView,
  },
  workflow: {
    list: WorkflowList,
    form: WorkflowForm,
    view: WorkflowView,
  },
}

class RouteResource {
  constructor(resource) {
    this.resource = resource;
    this.resourcePath = '/' + resource;

    this.plural = resource + 's';
    this.components = components[resource];
  }

  routes() {
    let routes = [];
    if (this.components.list != null) {
      routes.push(this.listRoute());
    }
    if (this.components.form != null) {
      routes.push(this.newRoute());
      routes.push(this.editRoute());
    }
    if (this.components.view != null) {
      routes.push(this.viewRoute());
    }

    return routes;
  }

  listRoute() {
    return { path: this.resourcePath, component: this.components.list, name: this.plural };
  }

  newRoute() {
    return { path: this.resourcePath + '/new', component: this.components.form, name: "new_" + this.resource, props: true };
  }

  viewRoute() {
    return {
      path: this.resourcePath + '/:id',
      component: this.components.view,
      name: this.resource,
      props: (route) => {
        return { id: Number(route.params.id) }
      },
    };
  }

  editRoute() {
    return {
      path: this.resourcePath + '/:id/edit',
      component: this.components.form,
      name: "edit_" + this.resource,
      props: (route) => {
        return { id: Number(route.params.id) }
      },
    };
  }
}

const routes = [
  { path: '/', component: Home, name: "home" },
  { path: '/login', component: Login, name: "login" },
  ...new RouteResource("appointment").routes(),
  ...new RouteResource("client").routes(),
  ...new RouteResource("matter").routes(),
  ...new RouteResource("referrer").routes(),
  ...new RouteResource("step").routes(),
  ...new RouteResource("task_template").routes(),
  ...new RouteResource("transaction").routes(),
  ...new RouteResource("user").routes(),
  ...new RouteResource("workflow").routes(),
  { path: '/report/audit', component: AuditReport, name: 'audit_report' },
  { path: '/report/referrer', component: ReferrerReport, name: 'referrer_report' },
  { path: '/report/outstanding_balances', component: OutstandingBalancesReport, name: 'outstanding_balances_report' },
]

Vue.mixin({
  beforeRouteEnter(to, from, next) {
    store.commit("clearAlerts");

    if (from.name === to.name) {
      return;
    }

    if (from.name === "login" || to.name === "login") {
      next();
      return;
    }

    if (User.isLoggedIn()) {
      if (to.name === "login") {
        return;
      }
    } else {
      next({ name: 'login' });
      store.commit("setAlertError", "Please login to continue");
      return;
    }

    if (User.loginExpired()) {
      User.logout();
      next({ name: 'login' });
      store.commit("setAlertError", "Please login to continue");
      return;
    }

    next();
  }
})

export const router = new VueRouter({routes})
new Vue({router, store, el: "#app", components: {App}, render: (h) => h(App), template: '<App/>'})
