<template>
  <div class="container">
    <div class="row my-3">
      <div class="col-lg">
        <h1>{{ title() }}</h1>
      </div>
    </div>

    <div class="row">
      <div class="col-lg">
        <b-form @submit="save">
          <b-form-group
            id="name_group"
            label="Name"
            label-for="name"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="name"
              v-model="workflow.name"
              type="text"
              required
            >
            </b-form-input>
          </b-form-group>

          <b-form-group
            id="description_group"
            label="Description"
            label-for="description"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-textarea
              id="textarea"
              v-model="workflow.description"
              rows="5"
              max-rows="5"
            />
          </b-form-group>

          <div class="text-right">
            <b-button @click="cancel" variant="link">Cancel</b-button>
            <b-button type="submit" variant="primary">Save</b-button>
          </div>
        </b-form>
      </div>
    </div>
  </div>
</template>

<script>
import Backend from '@/backend';
import {formatErrors} from "@/utils";

export default {
  name: "WorkflowForm",
  props: {
    id: Number,
  },
  data() {
    return {
      workflow: {},
    };
  },
  async created() {
    if (this.id) {
      this.getWorkflow();
    }
  },
  methods: {
    title() {
      return this.persisted() ? "Edit Workflow" : "New Workflow";
    },
    persisted() {
      return this.id != null;
    },
    getWorkflow() {
      Backend.getWorkflow(this.id).then(workflow => {
        this.workflow = workflow;
      }).catch(statusCode => {
        if (statusCode === 404) {
          this.$router.push({'name': 'workflows'});
          this.$store.commit("setAlertError", "Workflow not found.");
        } else {
          console.log(statusCode);
        }
      })
    },
    cancel() {
      if (this.persisted()) {
        this.$router.push({ name: 'workflow', params: { id: String(this.id) } });
      } else {
        this.$router.push({ name: 'workflows' });
      }
    },
    save() {
      if (this.persisted()) {
        Backend.updateWorkflow(this.id, this.workflow).then(() => {
          this.$router.push({ name: 'workflow', params: { id: String(this.id) } });
          this.$store.commit("setAlertSuccess", "Workflow updated successfully.");
        }).catch((errors) => {
          this.$store.commit("setAlertError", formatErrors("Workflow could not be saved", errors));
        });
      } else {
        Backend.createWorkflow(this.workflow).then(() => {
          this.$router.push({ name: 'workflows' });
          this.$store.commit("setAlertSuccess", "Workflow created successfully.");
        }).catch((errors) => {
          this.$store.commit("setAlertError", formatErrors("Workflow could not be saved", errors));
        });
      }
    }
  }
}
</script>

