<template>
  <div class="container">
    <div class="row my-3">
      <div class="col-lg-11">
        <h1>Transactions</h1>
      </div>
      <div class="col-lg">
        <b-button variant="primary" :to="{ 'name': 'new_transaction' }">New</b-button>
      </div>
    </div>
    <div class="row">
      <div class="col-lg">
        <transaction-table :transactions="transactions"></transaction-table>
      </div>
    </div>
  </div>
</template>

<script>
import TransactionTable from "@/components/TransactionTable.vue";
import Backend from "@/backend";

export default {
  name: "TransactionList",
  components: {
    "transaction-table": TransactionTable,
  },
  data() {
    return {
      transactions: [],
    };
  },
  async created() {
    this.getTransactions();
  },
  methods: {
    getTransactions() {
      Backend.getTransactions().then(transactions => {
        this.transactions = transactions;
      }).catch(() => {
        this.$store.commit("setAlertError", "Error retrieving list of transactions")
      });
    },
  },
}
</script>

