<template>
  <div>
    <b-input v-model="filter" type="text" placeholder="Type to filter"/>
    <b-table
        id="task-template-table"
        bordered
        striped
        show-empty
        responsive
        :items="taskTemplates"
        :fields="fields"
        sort-by="name"
        :filter="filter"
        :filter-included-fields="['name', 'description', 'staff_name']"
        :per-page="pageSize"
        :current-page="currentPage"
        @filtered="filtered"
    >
      <template #cell(name)="data">
        <router-link :to="{ name: 'edit_task_template', params: { id: data.item.id } }">{{ data.item.name }}</router-link>
      </template>
      <template #cell(actions)="data">
        <b-button @click="updateTaskTemplate(data.item.id)" size="sm" variant="light">Edit</b-button>
        <b-button @click="deleteTaskTemplate(data.item.id)" size="sm" variant="danger">Delete</b-button>
      </template>
    </b-table>
    <div class="overflow-auto">
      <b-pagination
          v-model="currentPage"
          :total-rows="rowCount"
          :per-page="pageSize"
          aria-controls="task-template-table"
      />
    </div>
  </div>
</template>

<script>
import {toLocalTime} from "@/convert_date";
import Backend from "@/backend";
import _ from 'lodash';
import {formatErrors} from "@/utils";

export default {
  name: "TaskTemplateTable",
  props: {
    taskTemplates: {
      type: Array,
      required: true,
    },
    stepId: {
      type: Number,
      required: true,
    },
    pageSize: {
      type: Number,
      default: 10,
    },
  },
  data() {
    return {
      filter: null,
      fields: [
        'name',
        'description',
        { key: 'staff_name', label: 'Staff' },
        { key: 'created', formatter: toLocalTime },
        { key: 'updated', formatter: toLocalTime },
        'actions',
      ],
      currentPage: 1,
      rowCount: null,
    };
  },
  created() {
    this.rowCount = this.taskTemplates.length;
  },
  methods: {
    updateTaskTemplate(taskTemplateId) {
      this.$router.push({ name: 'edit_task_template', params: { id: String(taskTemplateId), stepId: String(this.stepId) } });
    },
    deleteTaskTemplate(taskTemplateId) {
      if (confirm("Are you sure? This action cannot be undone.")) {
        Backend.deleteTaskTemplate(taskTemplateId).then(() => {
          this.taskTemplates = _.reject(this.taskTemplates, (tt) => tt.id === taskTemplateId);
          this.$store.commit("setAlertSuccess", "Task template deleted successfully.");
        }).catch(errors => {
          this.$store.commit("setAlertError", formatErrors("Task template could not be deleted", errors));
        })
      }
    },
    filtered(items) {
      this.rowCount = items.length;
      this.currentPage = 1;
    },
  }
}
</script>

