import _ from 'lodash';

export const toLocalTime = (date) => {
    return new Date(date + "Z").toLocaleString();
}

export const localDateAndTime = (date) => {
    let [d, t] = toLocalTime(date).split(", ");
    let [month, day, year] = d.split("/");
    month = _.padStart(month, 2, '0');
    day = _.padStart(day, 2, '0');
    d = year + "-" + month + "-" + day;

    let [fullTime, m] = t.split(" ");
    let [hours, minutes, seconds] = fullTime.split(":");
    if (m === "PM") {
        hours = String(Number(hours) + 12);
    }
    t = hours + ":" + minutes + ":" + seconds;

    return [d, t];
}

export const toUTC = (date) => {
    return new Date(date).toISOString().slice(0, -5);
}