<template>
  <div>
    <b-input v-model="filter" type="text" placeholder="Type to filter"/>
    <b-table
        id="transaction-table"
        bordered
        striped
        show-empty
        responsive
        :items="transactions"
        :fields="fields"
        sort-by="id"
        :filter="filter"
        :filter-included-fields="['matter_title', 'client_name', 'description', 'amount']"
        :per-page="pageSize"
        :current-page="currentPage"
        @filtered="filtered"
    >
      <template #cell(id)="data">
        <router-link :to="{ name: 'transaction', params: { id: data.item.id } }">{{ data.item.id }}</router-link>
      </template>
      <template #cell(matter)="data">
        <router-link :to="{ name: 'matter', params: { id: data.item.matter_id } }">{{ data.item.client_name }} - {{ data.item.matter_title }}</router-link>
      </template>
    </b-table>

    <div class="overflow-auto">
      <b-pagination
          v-model="currentPage"
          :total-rows="rowCount"
          :per-page="pageSize"
          aria-controls="transaction-table"
      />
    </div>
  </div>
</template>

<script>
import {toLocalTime} from "@/convert_date";
import {Money} from "@/money";
export default {
  name: "TransactionTable",
  props: {
      transactions: {
        type: Array,
        required: true,
      },
      excludeMatter: {
        type: Boolean,
        default: false,
      },
      pageSize: {
        type: Number,
        default: 10,
      },
  },
  data() {
    return {
      filter: null,
      rowCount: null,
      currentPage: 1,
    };
  },
  updated() {
    if (this.rowCount == null) {
      this.rowCount = this.transactions.length;
    }
  },
  computed: {
    fields() {
      let f = [
        { key: 'id', label: 'ID'},
        'description',
        { key: 'amount', formatter: this.padCurrency },
        { key: 'created', label: 'Date', formatter: toLocalTime },
      ];
      if (!this.excludeMatter) {
        f.splice(1, 0, 'matter');
      }
      return f;
    }
  },
  methods: {
    padCurrency(text) {
      return Money.format(Number(text));
    },
    filtered(items) {
      this.rowCount = items.length;
      this.currentPage = 1;
    },
  }
}
</script>

