<template>
  <div class="container">
    <div class="row my-3">
      <div class="col-lg">
        <h1>{{ title() }}</h1>
      </div>
    </div>

    <div class="row">
      <div class="col-lg">
        <b-form @submit="save">
          <b-form-group
            id="description_group"
            label="Description"
            label-for="description"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="description"
              v-model="transaction.description"
              type="text"
              required
            >
            </b-form-input>
          </b-form-group>

          <b-form-group
            id="matter_id_group"
            label="Matter"
            label-for="matter_id"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-select
              id="matter_id"
              v-model="transaction.matter_id"
              :options="matterOptions"
              required
            ></b-form-select>
          </b-form-group>

          <b-form-group
            id="amount_group"
            label="Amount"
            label-for="amount"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="amount"
              v-model="transaction.amount"
              type="number"
              step="0.01"
              value="0.00"
              required
            >
            </b-form-input>
            <small id="amountHelp" class="form-text text-muted">
              Use positive numbers to indicate charges, and negative numbers to indicate payments.
            </small>
          </b-form-group>

          <div class="text-right">
            <b-button @click="cancel" variant="link">Cancel</b-button>
            <b-button type="submit" variant="primary">Save</b-button>
          </div>
        </b-form>
      </div>
    </div>
  </div>
</template>

<script>
import Backend from '@/backend';
import {formatErrors} from "@/utils";
export default {
  name: "TransactionForm",
  props: {
    id: Number,
    matterId: Number,
  },
  data () {
    return {
      transaction: {},
      matterOptions: [],
    };
  },
  async created() {
    if (this.id) {
      this.getTransaction();
    }
    this.getMatterOptions();
    if (this.matterId) {
      this.transaction.matter_id = this.matterId;
    }
  },
  methods: {
    getTransaction() {
      Backend.getTransaction(this.id).then((txn) => {
        this.transaction = txn;
      }).catch(statusCode => {
        if (statusCode === 404) {
          this.$router.push({'name': 'transactions'});
          this.$store.commit("setAlertError", "Transaction not found.");
        } else {
          console.log(statusCode);
        }
      })
    },
    getMatterOptions() {
      Backend.getMatters().then(matters => {
        matters.forEach(matter => {
          this.matterOptions.push({value: matter.id, text: matter.client_name + " " + matter.title });
        });
      }).catch(() => {
        this.$store.commit("setAlertError", "Error loading matter options");
      });
    },
    title() {
      return this.persisted() ? "Edit Transaction" : "Add Transaction";
    },
    persisted() {
      return this.id != null;
    },
    cancel() {
      if (this.persisted()) {
        this.$router.push({ name: 'transaction', params: { id: String(this.id) } });
      } else {
        this.$router.push({ name: 'transactions' });
      }
    },
    save() {
      this.transaction.amount = Number(this.transaction.amount);
      if (this.persisted()) {
        Backend.updateTransaction(this.id, this.transaction).then(() => {
          this.$router.push({ name: 'transactions' });
          this.$store.commit("setAlertSuccess", "Transaction updated successfully.");
        }).catch((errors) => {
          this.$store.commit("setAlertError", formatErrors("Transaction could not be saved", errors));
        });
      } else {
        Backend.createTransaction(this.transaction).then(() => {
          this.$router.push({ name: 'transactions' });
          this.$store.commit("setAlertSuccess", "Transaction created successfully.");
        }).catch((errors) => {
          this.$store.commit("setAlertError", formatErrors("Transaction could not be saved", errors));
        });
      }
    }
  }
}
</script>

