<template>
  <div class="container">
    <div class="row my-3">
      <div class="col-lg-11">
        <h1>Clients</h1>
      </div>
      <div class="col-lg">
        <b-button variant="primary" :to="{ 'name': 'new_client' }">New</b-button>
      </div>
    </div>
    <div class="row">
      <div class="col-lg">
        <client-table :clients="clients"></client-table>
      </div>
    </div>
  </div>
</template>

<script>
import Backend from '@/backend';
import ClientTable from "@/components/ClientTable.vue";

export default {
  name: "ClientList",
  components: {
    "client-table": ClientTable,
  },
  data() {
    return {
      clients: [],
    }
  },
  async created() {
    this.loadClients();
  },
  methods: {
    loadClients() {
      Backend.getClients().then((clients) => {
        this.clients = clients;
      });
    },
  }
}
</script>

