<template>
  <div class="container">
    <div class="row" v-if="isLoading">
      <b-spinner variant="primary" label="Spinning"></b-spinner>
    </div>
    <div class="row mt-3" v-if="!isLoading">
      <div class="col-lg-10">
        <h1>{{ referrer.name }}</h1>
      </div>
      <div class="col-lg-2 text-right">
        <b-button :to="{ name: 'edit_referrer', params: { id: referrer.id } }" variant="light">Edit</b-button>
        <b-button @click="deleteReferrer" variant="danger">Delete</b-button>
      </div>
    </div>
    <div class="row" v-if="!isLoading">
      <div class="col-lg">
        <p>{{ referrer.email }}</p>
      </div>
    </div>
    <div class="row" v-if="!isLoading">
      <div class="col-lg">
        <h3>Clients from this referrer</h3>
        <client-table :clients="referrer.clients"></client-table>
      </div>
    </div>
  </div>
</template>

<script>
import Backend from "@/backend";
import ClientTable from "@/components/ClientTable.vue";
import {formatErrors} from "@/utils";

export default {
  name: "ReferrerView",
  components: {
    "client-table": ClientTable,
  },
  props: {
    id: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      referrer: null,
      isLoading: true,
    }
  },
  async created() {
    this.getReferrer();
  },
  methods: {
    getReferrer() {
      Backend.getReferrer(this.id).then((referrer) => {
        this.referrer = referrer;
        this.isLoading = false;
      }).catch((statusCode) => {
        if (statusCode === 404) {
          this.$store.commit("setAlertError", "Referrer not found.");
          this.$router.push( {name: 'referrers' });
        } else {
          console.log(statusCode);
        }
      });
    },
    deleteReferrer() {
      if (confirm("Are you sure? This action cannot be undone.")) {
        Backend.deleteReferrer(this.id).then(() => {
          this.$store.commit("setAlertSuccess", "Referrer deleted successfully.");
          this.$router.push({ name: 'referrers' });
        }).catch(errors => {
          this.$store.commit("setAlertError", formatErrors("Referrer could not be deleted", errors));
        })
      }
    }
  }
}
</script>

