<template>
  <div>
    <b-input v-model="filter" type="text" placeholder="Type to filter"/>
    <b-table
        id="client-table"
        bordered
        striped
        show-empty
        responsive
        :items="clients"
        :fields="fields"
        sort-by="last_name"
        :filter-included-fields="['last_name', 'middle_name', 'suffix', 'first_name']"
        :filter="filter"
        :per-page="pageSize"
        :current-page="currentPage"
        @filtered="filtered"
    >
      <template #cell(name)="data">
        <router-link :to="{ name: 'client', params: { id: data.item.id }}">{{ fullName(data.item) }}</router-link>
      </template>
      <template #cell(address)="data">
        <span v-html="address(data.item)"></span>
      </template>
    </b-table>

    <div class="overflow-auto">
      <b-pagination
          v-model="currentPage"
          :total-rows="rowCount"
          :per-page="pageSize"
          aria-controls="client-table"
      />
    </div>
  </div>
</template>

<script>
import {fullName} from "@/utils";
import {toLocalTime} from "@/convert_date";

export default {
  name: "ClientTable",
  props: {
    clients: {
      type: Array,
      required: true,
    },
    pageSize: {
      type: Number,
      default: 10,
    }
  },
  data() {
    return {
      fields: [
        'name',
        'address',
        'phone_number',
        'email',
        { key: 'created', formatter: toLocalTime },
        { key: 'updated', formatter: toLocalTime },
      ],
      filter: null,
      currentPage: 1,
      rowCount: null,
    }
  },
  updated() {
    if (this.rowCount == null) {
      this.rowCount = this.clients.length;
    }
  },
  methods: {
    fullName,
    address(client) {
      let addr = client.address_1 + "<br/>";
      if (client.address_2 != null) {
        addr += client.address_2 + "<br/>";
      }
      addr += client.city + ", " + client.state + " " + client.zip_code;
      return addr;
    },
    filtered(items) {
      this.rowCount = items.length;
      this.currentPage = 1;
    },
  }
}
</script>

