<template>
  <div class="container">
    <div class="row my-3">
      <div class="col-lg">
        <h1>Audit Report</h1>
      </div>
    </div>

    <div class="row">
      <div class="col-lg">
        <b-table bordered striped show-empty responsive :items="auditRecords" :fields="fields" sort-by="created" sort-desc/>
      </div>
    </div>
  </div>
</template>

<script>
import Backend from "@/backend";
import {toLocalTime} from "@/convert_date";

export default {
  name: "AuditReport",
  data() {
    return {
      auditRecords: [],
      fields: [
        { key: "id", label: "ID" },
        { key: 'created', formatter: toLocalTime },
        "message",
        { key: 'user_email', label: "User" },
      ]
    };
  },
  async created() {
    this.getAuditRecords();
  },
  methods: {
    getAuditRecords() {
      Backend.getReport("audit").then(auditRecords => {
        this.auditRecords = auditRecords;
      }).catch(statusCode => {
        console.log(statusCode);
      });
    }
  }
}
</script>

