<template>
  <div class="container">
    <div class="row" v-if="isLoading">
      <b-spinner variant="primary" label="Spinning"></b-spinner>
    </div>

    <div class="row my-3" v-if="!isLoading">
      <div class="col-lg-10">
        <h1>{{ workflow.name }}</h1>
      </div>
      <div class="col-lg-2 text-right">
        <b-button :to="{ name: 'edit_workflow', params: { id: workflow.id } }" variant="light">Edit</b-button>
        <b-button @click="deleteWorkflow" variant="danger">Delete</b-button>
      </div>
    </div>

    <div class="row" v-if="!isLoading">
      <div class="col-lg">
        <dl>
          <dt>Description</dt>
          <dd>{{ workflow.description }}</dd>
          <dt>Created</dt>
          <dd>{{ toLocalTime(workflow.created) }}</dd>
          <dt>Updated</dt>
          <dd>{{ toLocalTime(workflow.updated) }}</dd>
        </dl>
      </div>
    </div>

    <div class="row my-3" v-if="!isLoading">
      <div class="col-lg-11">
        <h3>Steps</h3>
      </div>
      <div class="col-lg-1">
        <b-button :to="{ name: 'new_step', params: { workflowId: this.workflow.id } }" variant="primary">Add</b-button>
      </div>
    </div>
      <div class="row" v-if="!isLoading">
      <div class="col-lg">
        <step-table :steps="workflow.steps" />
      </div>
    </div>

  </div>
</template>

<script>
import {toLocalTime} from "../convert_date";
import Backend from "@/backend";
import StepTable from "@/components/StepTable.vue";
import {formatErrors} from "@/utils";

export default {
  name: "WorkflowView",
  props: {
    id: {
      type: Number,
      required: true,
    },
  },
  components: {
    "step-table": StepTable,
  },
  data() {
    return {
      workflow: null,
      isLoading: true,
    };
  },
  async created() {
    this.getWorkflow();
  },
  methods: {
    toLocalTime,
    getWorkflow() {
      Backend.getWorkflow(this.id).then(workflow => {
        this.workflow = workflow;
        this.isLoading = false;
      }).catch(statusCode => {
        if (statusCode === 404) {
          this.$router.push({name: 'workflows'});
          this.$store.commit("setAlertError", "Workflow not found.");
        } else {
          console.log(statusCode);
        }
      });
    },
    deleteWorkflow() {
      if (confirm("Are you sure? This action cannot be undone.")) {
        Backend.deleteWorkflow(this.id).then(() => {
          this.$router.push({name: 'workflows'});
          this.$store.commit("setAlertSuccess", "Workflow deleted successfully.");
        }).catch(errors => {
          this.$store.commit("setAlertError", formatErrors("Workflow could not be deleted", errors));
        });
      }
    },
  },
}
</script>

