export const fullName = (client) => {
  let name = client.last_name;
  if (client.suffix != null) {
    name += " " + client.suffix;
  }
  name += ", " + client.first_name;
  if (client.middle_name) {
    name += " " + client.middle_name;
  }

  return name;
}

export const formatErrors = (msg, errors) => {
  let output = msg + ":<ul>";
  errors.forEach(err => {
    output += `<li>${err}</li>`
  })
  output += "</ul>";
  return output;
}
