<template>
    <div class="container">
    <div class="row my-3">
      <div class="col-lg">
        <h1>{{ title() }}</h1>
      </div>
    </div>

    <div class="row">
      <div class="col-lg">
        <b-form @submit="save">
          <b-form-group
            id="title_group"
            label="Title"
            label-for="title"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="title"
              v-model="appointment.title"
              type="text"
              required
            >
            </b-form-input>
          </b-form-group>

          <b-form-group
            id="matter_id_group"
            label="Matter"
            label-for="matter_id"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-select
              id="matter_id"
              v-model="appointment.matter_id"
              :options="matterOptions"
              required
            ></b-form-select>
          </b-form-group>

          <b-form-group
            id="staff_id_group"
            label="Staff"
            label-for="staff_id"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-select
              id="staff_id"
              v-model="appointment.staff_id"
              :options="staffOptions"
              required
            ></b-form-select>
          </b-form-group>

          <b-form-group
            id="description_group"
            label="Description"
            label-for="description"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-textarea
              id="textarea"
              v-model="appointment.description"
              rows="5"
              max-rows="5"
            />
          </b-form-group>

          <b-form-group
            id="location_group"
            label="Location"
            label-for="location"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="location"
              v-model="appointment.location"
              type="text"
              required
            >
            </b-form-input>
          </b-form-group>

          <b-form-group
            id="start_date_group"
            label="Start Date"
            label-for="startDate"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-datepicker
              id="startDate"
              v-model="appointment.startDate"
              required
            >
            </b-form-datepicker>
          </b-form-group>

          <b-form-group
            id="start_time_group"
            label="Start Time"
            label-for="startTime"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-timepicker
              id="startTime"
              v-model="appointment.startTime"
              locale="en"
              calendar-width="300"
              required
            >
            </b-form-timepicker>
          </b-form-group>

          <b-form-group
            id="end_date_group"
            label="End Date"
            label-for="endDate"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-datepicker
              id="endDate"
              v-model="appointment.endDate"
              required
            >
            </b-form-datepicker>
          </b-form-group>

          <b-form-group
            id="end_time_group"
            label="End Time"
            label-for="endTime"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-timepicker
              id="endTime"
              v-model="appointment.endTime"
              locale="en"
              calendar-width="300"
              required
            >
            </b-form-timepicker>
          </b-form-group>

          <div class="text-right">
            <b-button @click="cancel" variant="link">Cancel</b-button>
            <b-button type="submit" variant="primary">Save</b-button>
          </div>
        </b-form>
      </div>
    </div>
  </div>
</template>

<script>
import Backend from '@/backend';
import _ from 'lodash';
import {localDateAndTime, toUTC} from "@/convert_date";
import {formatErrors} from "@/utils";

export default {
  name: "AppointmentForm",
  props: {
    id: Number,
    matterId: Number,
    staffId: Number,
  },
  data() {
    return {
      appointment: {},
      matterOptions: [],
      staffOptions: [],
    };
  },
  async created() {
    if (this.id) {
      this.getAppointment();
    }
    this.getMatterOptions();
    this.getStaffOptions();
    if (this.matterId) {
      this.appointment.matter_id = this.matterId;
    }
    if (this.staffId) {
      this.appointment.staff_if = this.staffId;
    }
  },
  methods: {
    title() {
      return this.persisted() ? "Edit Appointment" : "New Appointment";
    },
    persisted() {
      return this.id != null;
    },
    getAppointment() {
      Backend.getAppointment(this.id).then((appt) => {
        this.appointment = appt;
        [this.appointment.startDate, this.appointment.startTime] = localDateAndTime(this.appointment.start);
        [this.appointment.endDate, this.appointment.endTime] = localDateAndTime(this.appointment.end);
      }).catch(statusCode => {
        if (statusCode === 404) {
          this.$router.push({'name': 'appointments'});
          this.$store.commit("setAlertError", "Appointment not found.");
        } else {
          console.log(statusCode);
        }
      })
    },
    getMatterOptions() {
      Backend.getMatters().then(matters => {
        matters.forEach(matter => {
          this.matterOptions.push({value: matter.id, text: matter.client_name + " " + matter.title });
        });
      }).catch(() => {
        this.$store.commit("setAlertError", "Error loading matter options");
      });
    },
    getStaffOptions() {
      Backend.getUsers().then(users => {
        users.forEach(user => {
          this.staffOptions.push({value: user.id, text: user.name });
        });
      }).catch(() => {
        this.$store.commit("setAlertError", "Error loading staff options");
      });
    },
    cancel() {
      if (this.persisted()) {
        this.$router.push({ name: 'appointment', params: { id: String(this.id) } });
      } else {
        this.$router.push({ name: 'appointments' });
      }
    },
    save() {
      this.appointment.start = toUTC(this.appointment.startDate + "T" + _.padStart(this.appointment.startTime, 8, '0'));
      this.appointment.end = toUTC(this.appointment.endDate + "T" + _.padStart(this.appointment.endTime, 8, '0'));

      if (this.persisted()) {
        Backend.updateAppointment(
            this.id,
            _.omit(this.appointment, ['startDate', 'endDate', 'startTime', 'endTime'])
        ).then(() => {
          this.$router.push({ name: 'appointments' });
          this.$store.commit("setAlertSuccess", "Appointment updated successfully.");
        }).catch((errors) => {
          this.$store.commit("setAlertError", formatErrors("Appointment could not be saved", errors));
        });
      } else {
        Backend.createAppointment(
            _.omit(this.appointment, ['startDate', 'endDate', 'startTime', 'endTime'])
        ).then(() => {
          this.$router.push({ name: 'appointments' });
          this.$store.commit("setAlertSuccess", "Appointment created successfully.");
        }).catch((errors) => {
          this.$store.commit("setAlertError", formatErrors("Appointment could not be saved", errors));
        });
      }
    },
  }

}
</script>

