<template>
  <div>
    <b-input v-model="filter" type="text" placeholder="Type to filter"/>
    <b-table
        id="appointmentsTable"
        bordered
        striped
        show-empty
        responsive
        :items="appointments"
        :fields="fields"
        sort-by="start"
        :filter-included-fields="['title', 'matter_title', 'client_name', 'location', 'staff_name']"
        :filter="filter"
        :per-page="pageSize"
        :current-page="currentPage"
        @filtered="filtered"
    >
      <template #cell(title)="data">
        <router-link :to="{ name: 'appointment', params: { id: data.item.id } }">{{ data.item.title }}</router-link>
      </template>
      <template #cell(matter)="data">
        <router-link :to="{ name: 'matter', params: { id: data.item.matter_id } }">{{ data.item.client_name }} - {{ data.item.matter_title }}</router-link>
      </template>
    </b-table>

    <div class="overflow-auto">
      <b-pagination
          v-model="currentPage"
          :total-rows="rowCount"
          :per-page="pageSize"
          aria-controls="appointmentsTable"
      />
    </div>
  </div>
</template>

<script>
import {toLocalTime} from "@/convert_date";

export default {
  name: "AppointmentTable",
    props: {
      appointments: {
        type: Array,
        required: true,
      },
      excludeMatter: {
        type: Boolean,
        default: false,
      },
      excludeStaff: {
        type: Boolean,
        default: false,
      },
      pageSize: {
        type: Number,
        default: 10,
      }
  },
  data() {
    return {
      filter: null,
      currentPage: 1,
      rowCount: null,
    }
  },
  computed: {
    fields() {
      let f = [
        "title",
        "location",
        { key: "start", formatter: toLocalTime },
        { key: "end", formatter: toLocalTime },
      ];

      if (!this.excludeMatter) {
        f.splice(1, 0, { key: "matter", label: "Matter" });
      }

      if (!this.excludeStaff) {
        f.push({ key: "staff_name", label: "Staff" });
      }

      return f;
    }
  },
  updated() {
    if (this.rowCount == null) {
      this.rowCount = this.appointments.length;
    }
  },
  methods: {
    filtered(items) {
      this.rowCount = items.length;
      this.currentPage = 1;
    },
  }
}
</script>

