import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from 'vuex-persistedstate';

Vue.use(Vuex);

export const store = new Vuex.Store({
    state: {
        alert: null,
        token: null,
        userId: null,
        userEmail: null,
        userName: null,
    },
    mutations: {
        setToken(state, token) {
            state.token = token;
        },
        setUserId(state, userId) {
            state.userId = userId;
        },
        setUserName(state, userName) {
            state.userName = userName;
        },
        setUserEmail(state, userEmail) {
            state.userEmail = userEmail;
        },
        setUserCanModifyUsers(state, canModifyUsers) {
            state.userCanModifyUsers = canModifyUsers;
        },
        setAlertSuccess(state, message) {
            state.alert = {alertType: 'success', message};
        },
        setAlertInfo(state, message) {
            state.alert = {alertType: 'info', message};
        },
        setAlertWarning(state, message) {
            state.alert = {alertType: 'warning', message};
        },
        setAlertError(state, message) {
            state.alert = {alertType: 'danger', message};
        },
        clearAlerts(state) {
            state.alert = null;
        },
        logout(state) {
            state.token = null;
            state.userId = null;
            state.userName = null;
            state.userEmail = null;
        }
    },
    getters: {
        getAlerts: state => {
            if (state.alert == null) {
                return [];
            }
            return [state.alert];
        },
        getToken: state => {
            return state.token;
        },
        getUserId: state => {
            return state.userId;
        },
        getUserName: state => {
            return state.userName;
        },
        getUserEmail: state => {
            return state.userEmail;
        },
        getUserCanModifyUsers: state => {
            return state.userCanModifyUsers;
        }
    },
    plugins: [
        createPersistedState({storage: window.sessionStorage})
    ]
});
