<template>
  <div class="container">
    <div class="row" v-if="isLoading">
      <b-spinner variant="primary" label="Spinning"></b-spinner>
    </div>
    <div class="row mt-3" v-if="!isLoading">
      <div class="col-lg-10">
        <h1>{{ matter.title }}</h1>
      </div>
      <div class="col-lg-2 text-right">
        <b-button :to="{ name: 'edit_matter', params: { id: matter.id } }" variant="light">Edit</b-button>
        <b-button @click="deleteMatter" variant="danger">Delete</b-button>
      </div>
    </div>

    <div class="row" v-if="!isLoading">
      <div class="col-lg">
        <dl>
          <dt>Client</dt>
          <dd>{{ matter.client_name }}</dd>
          <dt>Status</dt>
          <dd>{{ matter.status}}</dd>
          <dt>Workflow</dt>
          <dd>{{ matter.workflow_name || 'None' }}</dd>
          <dt>Step</dt>
          <dd>
            {{ matter.step_name || 'None' }}
            <a href="#" @click="advanceToNextStep" v-if="matter.next_step_id">Advance to next step</a>
          </dd>
        </dl>
      </div>
    </div>

    <hr/>

    <div class="row my-4" v-if="!isLoading">
      <div class="col-lg-11">
        <h3>
          Transactions
          <small class="text-muted small">
            Balance: {{ currentBalance() }}
          </small>
        </h3>
      </div>
      <div class="col-lg-1">
        <b-button variant="primary" :to="{ 'name': 'new_transaction', params: { 'matterId': this.id } }">New</b-button>
      </div>
    </div>
    <div class="row" v-if="!isLoading">
      <div class="col-lg">
        <transaction-table :transactions="matter.transactions" exclude-matter />
      </div>
    </div>

    <hr/>

    <div class="row my-4" v-if="!isLoading">
      <div class="col-lg-11">
        <h3>
          Appointments
        </h3>
      </div>
      <div class="col-lg-1">
        <b-button variant="primary" :to="{ 'name': 'new_appointment', params: { 'matterId': this.id } }">New</b-button>
      </div>
    </div>
    <div class="row" v-if="!isLoading">
      <div class="col-lg">
        <appointment-table :appointments="matter.appointments" exclude-matter />
      </div>
    </div>

        <hr/>

    <div class="row my-4" v-if="!isLoading">
      <div class="col-lg">
        <h3>
          Tasks
        </h3>
      </div>
    </div>
    <div class="row" v-if="!isLoading">
      <div class="col-lg">
        <task-table :tasks="matter.tasks" exclude-matter/>
      </div>
    </div>

    <hr/>

    <div class="row my-4" v-if="!isLoading">
      <div class="col-lg-11">
        <h3>Notes</h3>
      </div>
      <div class="col-lg-1">
        <b-button v-b-modal.add-note-modal variant="primary">Add</b-button>
      </div>
    </div>

    <div class="row" v-if="!isLoading">
      <div class="col-lg">
        <div v-if="matter.notes.length === 0">
          <b-card>
            <b-card-text>No notes yet :/</b-card-text>
          </b-card>
        </div>
        <b-card v-for="note in matter.notes" :key="note.id">
          <b-card-text>{{ note.text }}</b-card-text>
          <template #footer>
            {{ note.user_name }} ({{ note.user_email }}), {{ toLocalTime(note.created) }}
          </template>
        </b-card>
      </div>
    </div>
    <b-modal id="add-note-modal" title="Add note" ok-title="Save" @ok="addNote">
      <b-form>
        <b-form-group id="text_group">
          <b-form-textarea
            id="textarea"
            v-model="note.text"
            placeholder="Enter your note here"
            rows="5"
            max-rows="5"
            required
          >
          </b-form-textarea>
        </b-form-group>
      </b-form>
    </b-modal>
  </div>
</template>

<script>
import Backend from "@/backend";
import TransactionTable from "@/components/TransactionTable.vue";
import {Money} from "@/money";
import {toLocalTime} from "../convert_date";
import AppointmentTable from "@/components/AppointmentTable.vue";
import TaskTable from "@/components/TaskTable.vue";
import _ from 'lodash';
import {formatErrors} from "@/utils";

export default {
  name: "MatterView",
  components: {
    "appointment-table": AppointmentTable,
    "task-table": TaskTable,
    "transaction-table": TransactionTable,
  },
  props: {
    id: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      matter: null,
      isLoading: true,
      note: {
        matter_id: this.id,
        user_email: this.$store.getters.getUserEmail,
        user_name: this.$store.getters.getUserName,
      },
    };
  },
  async created() {
    this.getMatter();
  },
  methods: {
    toLocalTime,
    getMatter() {
      Backend.getMatter(this.id).then((matter) => {
        this.matter = matter;
        this.isLoading = false;
      }).catch(statusCode => {
        if (statusCode === 404) {
          this.$router.push({'name': 'matters'});
          this.$store.commit("setAlertError", "Matter not found.");
        } else {
          console.log(statusCode);
        }
      })
    },
    addNote() {
      let note = {...this.note};
      note.matter_id = this.id;
      note.user_email = this.$store.getters.getUserEmail;
      note.user_name = this.$store.getters.getUserName;
      Backend.createNote(note).then((note) => {
        this.matter.notes.push(note);
      })
    },
    currentBalance() {
      let total = 0;
      this.matter.transactions.forEach(txn => {
        total += Number(txn.amount);
      })
      return Money.format(total);
    },
    deleteMatter() {
      if (confirm("Are you sure? This action cannot be undone.")) {
        Backend.deleteMatter(this.id).then(() => {
          this.$router.push({ name: 'matters' });
          this.$store.commit("setAlertSuccess", "Matter deleted successfully.");
        }).catch(errors => {
          this.$store.commit("setAlertError", formatErrors("Matter could not be deleted", errors));
        })
      }
    },
    advanceToNextStep() {
      if (!this.matter.next_step_id) {
        this.$store.commit("setAlertError", "No next step defined for this matter's workflow");
        return;
      }
      if (_.some(this.matter.tasks, { status: "Incomplete"})) {
        if (confirm("Are you sure? You still have incomplete tasks.")) {
          this.matter.step_id = this.matter.next_step_id;
          Backend.updateMatter(this.id, this.matter).then(() => {
            this.getMatter();
            this.$store.commit("setAlertSuccess", "Matter moved to next step");
          }).catch(() => {
            this.$store.commit("setAlertError", "Matter could not be moved to next step");
          })
        }
      }
    }
  },
}
</script>

<style>
.modal-backdrop { opacity: 0.4; }
</style>

<style scoped>
.small { font-size: 1.25rem; }
</style>
