<template>
  <div class="container">
    <div class="row mt-3">
      <div class="col-lg">
        <h1>Welcome, {{ user.name }}!</h1>
      </div>
    </div>

    <div class="row mt-3">
      <div class="col-lg">
        <h3>Tasks to be completed</h3>
      </div>
    </div>

    <div class="row mt-3">
      <div class="col-lg">
        <task-table :tasks="tasks" exclude-staff />
      </div>
    </div>

    <hr/>

    <div class="row mt-3">
      <div class="col-lg">
        <h3>Upcoming Appointments</h3>
      </div>
    </div>

    <div class="row mt-3">
      <div class="col-lg">
        <appointment-table :appointments="appointments" exclude-staff />
      </div>
    </div>
  </div>
</template>

<script>
import AppointmentTable from "@/components/AppointmentTable.vue";
import Backend from "@/backend";
import {User} from "@/user";
import TaskTable from "@/components/TaskTable.vue";

export default {
  name: "Home",
  components: {
    "appointment-table": AppointmentTable,
    "task-table": TaskTable,
  },
  data() {
    return {
      user: {},
      appointments: [],
      tasks: [],
    }
  },
  async created() {
    this.getUserData();
  },
  methods: {
    getUserData() {
      Backend.getUser(User.getUserId()).then((user) => {
        this.user = user;
        const now = new Date();
        this.user.appointments.forEach(appt => {
          if (new Date(appt.start) > now || new Date(appt.end) > now) {
            this.appointments.push(appt);
          }
        });
        this.user.tasks.forEach(task => {
          if (task.status !== "Completed") {
            this.tasks.push(task);
          }
        })
      }).catch((statusCode) => {
        if (statusCode === 404) {
          this.$store.commit("setAlertError", "User not found.");
          this.$router.push({ name: 'users' });
        } else {
          console.log(statusCode);
        }
      });
    }
  },
}
</script>

