<template>
  <div class="container">
    <div class="row my-3">
      <div class="col-lg">
        <h1>{{ title() }}</h1>
      </div>
    </div>

    <div class="row">
      <div class="col-lg">
        <b-form @submit="save">
          <b-form-group
            id="name_group"
            label="Name"
            label-for="name"
            label-cols-lg="3"
            content-cols-lg="9"
          >
            <b-form-input
              id="name"
              v-model="referrer.name"
              type="text"
              required
            >
            </b-form-input>
          </b-form-group>

          <div class="text-right">
            <b-button @click="cancel" variant="link">Cancel</b-button>
            <b-button type="submit" variant="primary">Save</b-button>
          </div>
        </b-form>
      </div>
    </div>
  </div>
</template>

<script>
import Backend from '@/backend';
import {formatErrors} from "@/utils";
export default {
  name: "ReferrerForm",
  props: {
    id: Number,
  },
  data() {
    return {
      referrer: {},
    };
  },
  created() {
    if (this.id) {
      this.getReferrer();
    }
  },
  methods: {
    title() {
      return this.persisted() ? "Edit Referrer" : "Add Referrer";
    },
    persisted() {
      return this.id != null;
    },
    getReferrer() {
      Backend.getReferrer(this.id).then(referrer => {
        this.referrer = referrer;
      }).catch((statusCode) => {
        if (statusCode === 404) {
          this.$store.commit("setAlertError", "Referrer not found.");
          this.$router.push({ name: 'referrers' });
          return;
        }
        console.log(statusCode);
      });
    },
    cancel() {
      if (this.persisted()) {
        this.$router.push({ name: 'referrer', params: { id: String(this.id) } });
      } else {
        this.$router.push({ name: 'referrers' });
      }
    },
    save() {
      if (this.persisted()) {
        Backend.updateReferrer(this.id, this.referrer).then(() => {
          this.$store.commit("setAlertSuccess", "Referrer updated successfully.");
          this.$router.push({ name: 'referrers' });
        }).catch((errors) => {
          this.$store.commit("setAlertError", formatErrors("Referrer could not be saved", errors));
        });
      } else {
        Backend.createReferrer(this.referrer).then(() => {
          this.$store.commit("setAlertSuccess", "Referrer created successfully.");
          this.$router.push({ name: 'referrers' });
        }).catch((errors) => {
          this.$store.commit("setAlertError", formatErrors("Referrer could not be saved", errors));
        });
      }
    }
  },
}
</script>

